<template>
  <div v-if="currentCampaign" class="nav-container-dark campaign-special">
    <DeleteModal
        v-model="deletePromptVisible"
        :step-id="deleteId"
        :step-name="deleteName"
        @closed="deletePromptVisible = false"/>
    <el-select
        v-model="value"
        placeholder="Select"
        popper-class="campaign-select"
        class="campaign-select"
        @change="setCurrentCampaign">
      <el-option
          v-for="c in campaigns"
          :key="c.id"
          :label="c.name"
          :value="c.id">
      </el-option>
    </el-select>
    <draggable :list="currentCampaign.settings.steps" draggable=".campaign" @end="handleSaveCampaign">
      <div v-for="(s, index) in currentCampaign.settings.steps" :key="index" class="campaign"
           @click="handleStepChange(index)"
           :class="{'active': index == currentCampaignPage }">
        <span class="campaign-name" :title="s.name">
          {{ s.name }}
        </span>
        <span class="campaign-icons">
          <el-button circle class="copy" @click.stop="handleCopyStepByIndex(index)">
              <ii-copy/>
          </el-button>
        <el-button circle class="delete" @click.stop="handleDeleteStepByIndex(index, s.name)"
                   v-if="currentCampaign.settings.steps.length > 1">
              <ii-delete/>
        </el-button>
      </span>
      </div>
    </draggable>
    <div class="buttons">
      <el-button type="primary" id="new-page-button" @click="handleNewStep">
        New Page
      </el-button>
      <el-upload
          action=""
          :auto-upload="false"
          :show-file-list="false"
          :on-change="onChange"
          :limit="1"
          accept=".zip">
        <el-button type="info" id="import-button">
          Import Template
        </el-button>
        <div class="error" v-if="importFail">
          ZIP file is not valid
        </div>
      </el-upload>
    </div>
  </div>
</template>

<script>
import {mapActions, mapGetters} from "vuex";
import DeleteModal from "@/components/DeleteModal";

export default {
  name: "CampaignSpecialSidebar",
  components: {DeleteModal},
  data: () => ({
    value: null,
    importFail: false,
    deletePromptVisible: false,
    deleteId: '',
    deleteName: '',
  }),
  computed: {
    ...mapGetters(['currentCampaign', 'currentCampaignPage', 'campaigns']),
  },
  watch: {
    currentCampaign: {
      handler(v) {
        if (v) {
          this.value = v.id;
        }
      },
      deep: true,
      immediate: true,
    },
  },
  methods: {
    ...mapActions([
      'setCurrentStep',
      'addStep',
      'deleteStep',
      'setCurrentCampaign',
      'importStep',
      'duplicateStep',
      'saveCampaign'
    ]),
    handleSaveCampaign() {
      this.saveCampaign()
    },
    handleNewStep() {
      this.addStep()
    },
    handleDeleteStepByIndex(index, name) {
      this.deleteId = index
      this.deleteName = name
      this.deletePromptVisible = true
    },
    handleCopyStepByIndex(index) {
      if (!this.currentCampaign.settings.steps[index].page) {
        this.handleNewStep()
      } else {
        this.duplicateStep(index)
      }
    },
    handleStepChange(index) {
      this.setCurrentStep(index)
    },
    onChange(file, list) {
      this.importFail = false
      this.importStep(file.raw).then((res) => {
        list.length = 0
        if (res.error) {
          this.importFail = true
        }
      })
    },
  }
}
</script>

<style lang="scss">
.campaign-special {

  .el-upload {
    width: 100%;
  }

  .el-select {
    width: 100%;

    .el-input {
      &__inner {
        height: 70px;
        font-weight: 500;

        padding-left: 20px !important;
        font-size: 16px !important;
      }
    }
  }

}
</style>

<style lang="scss" scoped>

.sidebar {
  position: relative;
  width: 265px;
  height: 100%;
  display: flex;
  transition: all 0.4s ease, height 0s ease;
  z-index: 1;

  .error {
    color: #f0c600;
  }

  &-wrapper {
    width: 265px;
    display: flex;
  }

  .el-button {
    width: 100%;
    margin-left: 0;

    &#new-page-button {
      background-color: $clr-btn-primary;

      &:hover {
        background-color: $clr-btn-hover;
      }
    }

    &#import-button {
      color: #1b4000;

      &:hover {

      }
    }
  }
}

.campaign {
  height: 57px;
  background-color: $clr-nav-bg;
  padding: 0 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  cursor: pointer;
  color: $clr-txt-nav;
  transition: all ease 0.3s;

  &.active, &:hover {
    background-color: $clr-primary;
    color: white !important;
  }

  &-name {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }

  &-icons {
    min-width: 70px;
    display: flex;
    justify-content: space-evenly;

    .el-button {
      width: 30px;
      height: 30px;
      background-color: transparent;
      color: $clr-txt-nav;
      padding: 0;

      &:hover {
        color: white;
        transform: rotate(12deg);
      }

      .svg {
        width: 100%;
        height: 100%;
        margin: 0;
      }

      .copy {

      }

      .delete {

      }
    }
  }

}

.buttons {
  padding: 0 12px;
  margin-top: 25px;

  .el-button {
    margin-bottom: 15px;
  }
}


.el-menu {
  width: 265px;
  display: flex;
  flex-direction: column;
  border: none;

  &::before, &::after {
    display: none;
  }

  &-item {
    height: 34px;
    margin-bottom: 6px;
    padding-left: 40px !important;
    padding-right: 0;
    color: $clr-txt-nav;
    line-height: 34px;
    background-color: transparent !important;
    transition: all 1s ease;

    &:hover {
      color: white;
    }

    &:active, &.is-active {
      color: $clr-primary;
    }

    span {
      transition: opacity 1s ease;
      opacity: 1;
    }
  }

  .nav-container {
    background-color: $clr-nav-bg;
    padding-bottom: 52px;
    font-weight: 700;
    transition: all 1s ease;

    &-dark {
      transition: all 1s ease;
      background-color: $clr-nav-bg-dark;
      flex-grow: 1;
    }
  }

  .nav-section {
    color: white;
    font-weight: 500;
    transition: all 1s ease;
    height: 17px;
    opacity: 1;
  }
}

.collapsed {
  width: 36px;

  .nav-section {
    overflow: hidden;
    opacity: 0;
    height: 0;
    margin-top: 0;
    margin-bottom: 0;
    transform: translate(-30px, -15px);
  }

  .nav-container, .nav-container-dark {
    padding: 23px 8px;
    background-color: $clr-nav-bg-dark;
  }

  .el-menu-item {
    padding: 0 !important;

    svg {
      transition: all ease 0.2s;

      &:hover {
        transform: scale(1.06) rotate(10deg);
      }
    }

    span {
      opacity: 0;
    }
  }
}
</style>
