<template>
  <div class="subscribe">
    <el-button id="subscribers" @click="handleSubscribers"><ii-people/>Subscribers</el-button>
    <el-button id="settings" @click="handleSettings"> <ii-settings/>Settings</el-button>
  </div>
</template>
<script>
import {mapActions} from "vuex";

export default {
  name: "Subscribers",
  methods: {
    ...mapActions(['loadEmails']),
    handleSubscribers() {
      this.$emit('toggle-subscribers')
      this.loadEmails()
    },
    handleSettings() {
      this.$emit('toggle-settings')
    }
  }
}
</script>

<style lang="scss" scoped>
.subscribe {
  width: 190px;
  height: 30px;

  display: flex;
  justify-content: space-between;

  svg {
    margin-right: 10px;
  }

  .el-button {

    height: 30px;
    padding: 0 10px;

    font-size: 10px !important;
    font-weight: 500 !important;
    color: $clr-primary;
    border: 1px solid $clr-funnel !important;
    background-color: transparent;

    &:hover {
      background-color: $clr-bg-light;
    }
  }
}
</style>
