import axios from 'axios';
import router from '@/router';

const config = {
    baseURL: 'https://launchigloo.com/api/',
    headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
    },
};

const authRedirect = () => {
    localStorage.removeItem('token');
    if (router.currentRoute.name !== 'Login') {
        router.push('/login');
    }
};

const Http = axios.create(config);

Http.interceptors.request.use(
    async (replaceConfig) => {
        const token = localStorage.getItem('token');

        let d = new FormData();

        if (replaceConfig.data instanceof FormData) {
            d = replaceConfig.data;
        }

        if (replaceConfig.data) {
            Object.entries(replaceConfig.data).forEach((entry) => {
                if (typeof entry[1] == 'object') {
                    d.append(entry[0], JSON.stringify(entry[1]));
                } else {
                    d.append(entry[0], entry[1]);
                }
            });
        }

        if (token) {
            d.append('token', token);
        }

        replaceConfig.data = d;

        return replaceConfig;
    },
    error => Promise.reject(error),
);

Http.interceptors.response.use((response) => {
    if (
        !localStorage.getItem('token')
        && !(response
            && response.data
            && response.data.jwt
            && response.data.jwt.token)
    ) {
        if (
            router.currentRoute.name !== 'Login'
        ) {
            router.push('/login');
        }
    }

    return response;
}, (error) => {
    if (error && error.response) {
        switch (error.response.status) {
            case 401:
                authRedirect();
                break;
            case 403:
                authRedirect();
                break;
            case 404:
                authRedirect();
                break;
            default:
                break;
        }
    }

    return Promise.reject(error);
});

export default Http;
