/* eslint-disable */
export default function (http) {
    return {
        getAllCampaigns() {
            return new Promise((res, rej) => {
                http.post('campaign/list')
                    .then((data) => {
                        let campaigns = data.data;

                        if (campaigns && campaigns.length > 0) {
                            campaigns = campaigns.map(c => ({...c, settings: JSON.parse(c.settings)}));
                        }

                        res(campaigns);
                    })
                    .catch(err => rej(err))
            });
        },
        getCampaign(campaignId) {
            return new Promise((res, rej) => {
                http.post('campaign/one', {id: +campaignId})
                    .then((data) => {
                        let campaign = {
                            id: campaignId,
                            name: data.data.name,
                            settings: data.data,
                            domain: data.data.domain,
                            home_page_id: data.data.home_page_id,
                            out_site: data.data.out_site,
                            prjThumb: data.data.prjThumb
                        };

                        // if (campaign) {
                        //     campaign = (campaign => ({...campaign, settings: JSON.parse(campaign.settings)}));
                        // }

                        res(campaign)
                    }).catch(err => rej(err))
            });
        },
        createCampaign(campaignName) {
            return new Promise((res, rej) => {
                http.post('campaign/create', {
                    campaign: {
                        name: campaignName,
                        steps: [{name: "- no name -", page: ''}]
                    }
                })
                    .then((data) => {
                        res(data.data)
                    }).catch(err => rej(err))
            });
        },
        deleteCampaign(id) {
            return new Promise((res, rej) => {
                http.post('campaign/delete', {id})
                    .then((data) => {
                        res(data.data)
                    }).catch(err => rej(err))
            });
        },
        saveCampaign(payload) {
            const f = new FormData()
            f.append('id', +payload.id)
            f.append('campaign', JSON.stringify(payload.settings))

            if(payload.general) {
                const flattenedSettings = convertJSONAssoc({general: payload.general})

                flattenedSettings.forEach((el) => {
                    let newKey = el.key.replace('[', '')
                    newKey = newKey.replace(']', '')
                    f.append(newKey, el.val)
                })
            }


            return new Promise((res, rej) => {
                http.post('campaign/save', f) // {id: +payload.id, campaign: payload.settings}
                    .then((data) => {
                        res(data.data)
                    }).catch(err => rej(err))
            });
        },
        duplicate(campaignId) {
            return new Promise((res, rej) => {
                http.post('campaign/duplicate', {id: +campaignId})
                    .then((data) => {
                        res(data.data)
                    }).catch(err => rej(err))
            });
        },
        duplicateStep(stepId) {
            return new Promise((res, rej) => {
                http.post('page/duplicate', {id: +stepId})
                    .then((data) => {
                        res(data.data)
                    }).catch(err => rej(err))
            });
        },
        getPages() {
            return new Promise((res, rej) => {
                http.post('page-templates/list?type=new', {type: 'new'})
                    .then((data) => {
                        res(data.data)
                    }).catch(err => rej(err))
            });
        },
        getPagesOld() {
            return new Promise((res, rej) => {
                http.post('page-templates/list?type=old', {type: 'old'})
                    .then((data) => {
                        res(data.data)
                    }).catch(err => rej(err))
            });
        },
        setStepTemplate(campaignId, templateId, type) {
            return new Promise((res, rej) => {
                http.post('page/create', {
                    id: +campaignId,
                    tpl: +templateId || '',
                    type
                }).then((data) => {
                    res(data.data)
                }).catch(err => rej(err))
            });
        },
        savePageHf(pageId, header, footer) {
            return new Promise((res, rej) => {
                http.post('page/save-hf', {
                    id: +pageId,
                    header,
                    footer
                }).then((data) => {
                    res(data.data)
                }).catch(err => rej(err))
            });
        },
        exportPage(pageId, pageCode) {
            return new Promise((res, rej) => {
                http.post('page/export', {
                    id: +pageId,
                    code: pageCode,
                }).then((data) => {
                    res(data.data)
                }).catch(err => rej(err))
            })
        },
        exportPageJson(page) {
            return new Promise((res, rej) => {
                http.post('page/export-json', page).then((data) => {
                    res(data.data)
                }).catch(err => rej(err))
            })
        },
       importPage(page) {
           const f = new FormData()
           f.append('id', +page.id)
           f.append('campaign_id', page.id)
           f.append('file', page.file, page.file.name)

            return new Promise((res, rej) => {
                http.post('page/import',  f).then((data) => {
                    res(data.data)
                }).catch(err => rej(err))
            })
        },
        takeScreenshot(page) {
            return new Promise((res, rej) => {
                http.post('page/take-screenshot', page).then((data) => {
                    res(data.data)
                }).catch(err => rej(err))
            });
        }
    }
}

/**
 * Formats the JSON result to an associative array, concatenating child and parent keys
 */
function convertJSONAssoc(obj, firstlevel) {
    // consider string, number and boolean values in JSON as the last
    // elements and can't be recursed into any further
    if (typeof obj == 'string' || typeof obj == 'number' || typeof obj == 'boolean') {
        var ab = [];
        var bc = {};
        bc.key = '';
        bc.val = obj;
        ab.push(bc);
        return ab;
    }

    // the top most call which builds the final result
    if (firstlevel) {
        var result = {};
        for (key in obj) {
            var val = obj[key];
            var s = convertJSONAssoc(val, false);
            for (var o = 0; o < s.length; o++) {
                var v = s[o];
                result[key + v['key']] = v['val'];
            }
        }
        return result;
    } else {
        // this is where the recursion happens. As long as objects are found,
        // we use the code below to keep on parsing obj keys
        var paths = [];
        for (var key in obj) {
            var val = obj[key];
            var s = convertJSONAssoc(val, false);
            for (var o = 0; o < s.length; o++) {
                var v = s[o];
                var de = {};
                de.key = "[" + key + "]" + v['key'];
                de.val = v['val'];
                paths.push(de);
            }
        }
        return paths;
    }
}
