import user from './user';
import campaign from "./campaign";
import autoresponder from "@/api/autoresponder";
import contest from "@/api/contest";
import admin from "@/api/admin";

export default function (http) {
    return {
        user: user(http),
        campaign: campaign(http),
        autoresponder: autoresponder(http),
        contest: contest(http),
        admin: admin(http),
    }
}
