<template>
  <div class="title">
    <span class="title-text">{{ title || 'Dashboard' }}</span>
  </div>
</template>

<script>
  export default {
    data: () => ({
      title: '',
    }),
    watch: {
      '$route': {
        immediate: true,
        deep: true,
        handler(val) {
          if (val.name) {
            this.title = val.name;
          }
        }
      }
    },
  }
</script>

<style lang="scss" scoped>
.title {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 20px;
  width: 100%;
  flex: 1;
  color: $clr-txt-topbar-dark;

&-text {
   padding-left: 50px;
 }
}

.el-main .title {
  display: none;
  margin-top: -25px;
  margin-bottom: 25px;
  background-color: white;
  height: 83px;
}

@media (max-width: $media-tablet) {
  .el-header .title {
    display: none;
  }

  .el-main .title {
    display: flex;
  }
}
</style>


