              <el-option label="Downsell" value="downsell"/>
              <el-option label="Monthly" value="monthly"/>
              <el-option label="Youzign" value="youzign"/>
              <el-option label="Membership" value="membership"/>
              <el-option label="Ecommerce pack" value="ecommerce"/>
            </el-select>
            <el-button class="active contains-icon" @click="preview(idx)">
              <fa-preview/>
            </el-button>
          </div>
          <div class="row date">
            Publish date:
            <div class="date">
              <el-select v-model="templates[idx].month">
                <el-option value="01"/>
