                <el-option value="2018"/>
                <el-option value="2019"/>
                <el-option value="2020"/>
                <el-option value="2021"/>
                <el-option value="2022"/>
                <el-option value="2023"/>
                <el-option value="2024"/>
                <el-option value="2025"/>
              </el-select>
            </div>
          </div>
          <div class="row">{{ c.user_email }}</div>
          <div class="row category">
            <el-select v-model="templates[idx].settings.tpl_cat">
              <!--  v-for  -->
              <el-option v-for="cat in templateCategories" :key="cat" :value="cat"/>
            </el-select>
          </div>
          <div class="row">
