<script>
export default {
  name: "Templates",
  data: () => ({
    saveSuccess: false,
    pages: null,
    pagesGrouped: null,
    loading: false,
    selectedFilter: 'optin',
    templateCategories: [
      // 'all',
      "optin",
      "landing",
      "membership",
      "webinar",
      "upsell",
      "affiliate",
      "download",
      "pre-launch",
      "landing_homepage",
      "membership_dashboard",
      "course_page",
      "lesson_page",
    ],
  }),
  methods: {
    saveTemplate(idx) {
      // loading
      this.$api.admin.saveTemplate(this.templates[idx], idx)
      // .then((res) => {
      //   // this.saveSuccess = true
      //   console.log(res)
      // })
      // .catch()
      // .finally()
      // end loading
    },
    groupBy(xs, key) {
      return xs.reduce(function (rv, x) {
        (rv[x[key]] = rv[x[key]] || []).push(x);
        return rv;
      }, {});
    },
    preview(idx) {
      const current = this.templates[idx]
      window.open(`https://launchigloo.com/preview/${current.id}?c=${current.code}`, '_blank')
    },
    updateThumb(idx) {
      const current = this.templates[idx]
      this.$set(current, 'loading', true)
      this.$api.admin.refreshThumb(current.id, current.code).then((res) => {
        current.thumb = res.url
      }).finally(() => {
        this.$set(current, 'loading', false)
      })
    }
  },
  created() {
    this.loading = true
    this.$api.admin.getPageTemplates()
        .then(res => {
          this.pages = res
          // this.pagesGrouped = this.groupBy(Object.keys(res).reduce(function (r, k) {
          //   return r.concat(res[k]);
          // }, []), 'date_publish')
        })
        .finally(() => (
            this.loading = false
        ))
  },
  computed: {
    templates() {
      return this.pages[this.selectedFilter] || []
    },
    templatesAll() {
      if (this.pages) {
        const vm = this
        let flat = Object.keys(vm.pages).reduce(function (r, k) {
          return r.concat(vm.pages[k]);
        }, [])
        return this.groupBy(flat, 'date_publish')
      }
      return []
    }
  }
}
</script>

