  .filters {
    align-self: flex-start;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-size: 14px;
    font-weight: 500;
    flex-wrap: wrap;

    @media (max-width: 800px) {
      display: none;
    }

    &-mobile {
      display: none;
      margin-left: auto;

      @media (max-width: 800px) {
        display: block;
      }
    }

    #team {
      margin-right: 15px;
    }

    &__inner {
      display: flex;
      align-items: flex-start;
      flex-wrap: wrap;
      justify-content: flex-start;
      height: 100%;
      flex-direction: row;
      flex: 1;
    }

    .filter {
      height: 16px;
      margin: 6px 25px 6px 0;
      display: flex;
      align-items: center;
      color: $clr-txt-topbar;
      transition: all 0.2s;

      cursor: pointer;

      &:hover, &.active {
        color: $clr-primary-light;
      }

      &.hidden {
        display: none;
      }

      svg {
        margin-right: 7px;
      }
    }
  }

  .cards {
    display: flex;
    gap: 25px;
    flex-wrap: wrap;
    justify-content: flex-start;

    &.date-group {
      flex-direction: column;
    }

    @media (max-width: $media-tablet) {
      justify-content: center;
    }

    .card {
      display: flex;
      flex-direction: column;
      gap: 15px;
      align-items: flex-start;

      background-color: $clr-bg-light;
      border-radius: 8px;


      box-shadow: 0px 0px 28px rgba(0, 0, 0, 0.05);

      width: 360px;
      height: 630px;
      padding: 14px 10px;

      position: relative;

      &-thumb {
        background-color: $clr-grey-light;
        width: 260px;
        min-height: 280px;
        align-self: center;

        position: relative;

        .refresh {
          display: flex;
          justify-content: center;
          align-items: center;
          position: absolute;
          top: 10px;
          right: 10px;
          width: 27px;
          height: 27px;
          border-radius: 50%;
          background-color: $clr-primary;
          color: white;
          padding: 6px;
          cursor: pointer;
          transition: all ease 0.3s;

          &:hover {

            transform: rotate(20deg);
          }

          svg {
            color: white;
          }
        }

        img {
          width: 100%;
          height: 100%;
        }
      }

      &-name {

        align-self: center;
      }

      &-controls {

        display: flex;
        flex-direction: column;
        justify-content: space-between;
        //gap: 8px;

        width: 100%;
        height: 100%;

        .row {

          display: flex;
          justify-content: space-between;
          align-items: center;

          width: 100%;
          height: 40px;
        }
      }
    }
