<template>
  <div class="contest" v-if="contests.length">
    <div class="row">
      <div class="text">Contest Name</div>
      <div class="control">
        <div class="input-short">
          <el-input v-model="form.contestName"/>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="text">Popup Background</div>
      <div class="control">
        <div class="color">
          <el-input v-model="form.popupBackground"/>
        </div>
        <el-color-picker v-model="form.popupBackground"/>
      </div>
    </div>
    <div class="row">
      <div class="text">Text Color</div>
      <div class="control">
        <div class="color">
          <el-input v-model="form.textColor"/>
        </div>
        <el-color-picker v-model="form.textColor"/>
      </div>
    </div>
    <div class="row">
      <div class="text">Share Icon</div>
      <div class="control">
        <div class="color">
          <el-input v-model="form.shareIcon"/>
        </div>
        <el-color-picker v-model="form.shareIcon"/>
      </div>
    </div>
    <div class="row">
      <div class="text">Icon Placement</div>
      <div class="control">
        <div class="select">
          <el-select v-model="form.iconPlacement">
            <el-option label="Bottom-Left" value="left"></el-option>
            <el-option label="Bottom-Right" value="right"></el-option>
          </el-select>
        </div>
      </div>
    </div>
    <div class="row multiple-inputs">
      <div class="text">End</div>
      <div class="multiple-inputs-container">
        <div class="control">
          <div class="select">
            <el-select v-model="form.end">
              <el-option label="yes" :value="true"></el-option>
              <el-option label="no" :value="false"></el-option>
            </el-select>
          </div>
        </div>
        <div class="control">
          <div class="select" v-if="form.end">
            <el-date-picker v-model="form.date" default-time="12:00:00"/>
          </div>
        </div>
        <div class="control">
          <div class="select" v-if="form.end">
            <el-time-picker v-model="form.time" format="HH:mm"/>
          </div>
        </div>
        <div class="control timezone">
          <div class="select" v-if="form.end">
            <el-select v-model="form.timeZone">
              <el-option label="-12.0 (Eniwetok, Kwajalein)" value="-12"/>
              <el-option label="-11.0 (Midway Island, Samoa)" value="-11"/>
              <el-option label="-10.0 (Hawaii)" value="-10"/>
              <el-option label="-9.0 (Alaska)" value="-9"/>
              <el-option label="-8.0 (Pacific Time - US &amp; Canada)" value="-8"/>
              <el-option label="-7.0 (Mountain Time - US &amp; Canada)" value="-7"/>
              <el-option label="-6.0 (Central Time - US &amp; Canada, Mexico City)" value="-6"/>
              <el-option label="-5.0 (Eastern Time - US &amp; Canada, Bogota, Lima)" value="-5"/>
              <el-option label="-4.0 (Atlantic Time Canada, Caracas, La Paz)" value="-4"/>
              <el-option label="-3.5 (Newfoundland)" value="-3.5"/>
              <el-option label="-3.0 (Brazil, Buenos Aires, Georgetown)" value="-3"/>
              <el-option label="-2.0 (Mid-Atlantic)" value="-2"/>
              <el-option label="-1.0 (Azores, Cape Verde Islands)" value="-1"/>
              <el-option label="0.0 (Western Europe Time, London, Lisbon, Casablanca)" value="0"/>
              <el-option label="+1.0 (Brussels, Copenhagen, Madrid, Paris)" value="1"/>
              <el-option label="+2.0 (Kaliningrad, South Africa)" value="2"/>
              <el-option label="+3.0 (Baghdad, Riyadh, Moscow, St. Petersburg)" value="3"/>
              <el-option label="+3.5 (Tehran)" value="3.5"/>
              <el-option label="+4.0 (Abu Dhabi, Muscat, Baku, Tbilisi)" value="4"/>
              <el-option label="+4.5 (Kabul)" value="4.5"/>
              <el-option label="+5.0 (Ekaterinburg, Islamabad, Karachi, Tashkent)" value="5"/>
              <el-option label="+5.5 (Bombay, Calcutta, Madras, New Delhi)" value="5.5"/>
              <el-option label="+5.75 (Kathmandu)" value="5.75"/>
              <el-option label="+6.0 (Almaty, Dhaka, Colombo)" value="6"/>
              <el-option label="+7.0 (Bangkok, Hanoi, Jakarta)" value="7"/>
              <el-option label="+8.0 (Beijing, Perth, Singapore, Hong Kong)" value="8"/>
              <el-option label="+9.0 (Tokyo, Seoul, Osaka, Sapporo, Yakutsk)" value="9"/>
              <el-option label="+9.5 (Adelaide, Darwin)" value="9.5"/>
              <el-option label="+10.0 (Eastern Australia, Guam, Vladivostok)" value="10"/>
              <el-option label="+11.0 (Magadan, Solomon Islands, New Caledonia)" value="11"/>
              <el-option label="+12.0 (Auckland, Wellington, Fiji, Kamchatka)" value="12"/>
            </el-select>
          </div>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="text">Share Description</div>
      <div class="control">
        <div class="input-long">
          <el-input v-model="form.shareDescription"/>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="text">Collected Points Text</div>
      <div class="control">
        <div class="input-long">
          <el-input v-model="form.collectedPointsText"/>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="text">Rank Text</div>
      <div class="control">
        <div class="input-long">
          <el-input v-model="form.rankText"/>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="text">YouTube Video</div>
      <div class="control">
        <div class="input-long">
          <el-input v-model="form.youTubeVideo"/>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="btn">
        <el-button type="warning" @click="handleSaveContest">Save Changes</el-button>
      </div>
    </div>
  </div>
  <div class="contest create-contest" v-else> Create a new Contest in the sidebar on the left!</div>
</template>

<script>

import {mapActions, mapGetters} from "vuex";

export default {
  name: "Contest",
  components: {},
  data: () => ({
    form: {
      contestName: '',
      popupBackground: '#ECE7C9',
      textColor: '#444',
      shareIcon: '#39bed7',
      iconPlacement: '',
      end: '',
      date: '',
      time: '',
      timeZone: '',
      shareDescription: '',
      collectedPointsText: '',
      rankText: '',
      youTubeVideo: '',
    }
  }),
  watch: {
    currentContest: {
      handler(s) {
        if (s) {
          this.form.contestName = s.name
          this.form.popupBackground = s.settings.bgColor
          this.form.textColor = s.settings.color
          this.form.shareIcon = s.settings.iconColor
          this.form.iconPlacement = s.settings.iconPosition
          this.form.end = s.settings.end_date
          this.form.date = s.settings.date
          this.form.time = new Date()
          this.form.time.setHours(s.settings.hours, s.settings.minutes)
          this.form.timeZone = s.settings.timezone
          this.form.shareDescription = s.settings.share_description
          this.form.collectedPointsText = s.settings.points_description
          this.form.rankText = s.settings.rank_description
          this.form.youTubeVideo = s.settings.youtube_url
        }
      },
      deep: true,
      immediate: true
    }
  },
  computed: {
    ...mapGetters(['currentContest', 'contests'])
  },
  methods: {
    ...mapActions(['saveCurrentContest']),
    handleSaveContest() {
      this.saveCurrentContest(this.form)
    }
  }
}
</script>

<style lang="scss">
.contest {

  .multiple-inputs-container {

    .el-input {

      width: 140px !important;
    }
  }

  .el-input__icon {
    line-height: 33px !important;
  }

  .el-color-picker {
    border-radius: 4px !important;

    &__trigger {
      width: 90px !important;
      height: 33px !important;
      padding: 0 !important;
      border: none !important;
      border-radius: 4px !important;
    }

    &__color {
      width: 90px !important;
      height: 33px !important;
      padding: 0 !important;
      border: none !important;
      border-radius: 4px !important;

      &-inner {

        border-radius: 4px !important;
      }
    }
  }

  .el-input, .el-select {

    &__inner {
      height: 33px !important;
      line-height: 33px !important;
    }
  }

  .el-icon-arrow {
    &-up, &-down {
      line-height: 33px !important;
    }
  }

  .btn {
    .el-button {
      height: 30px;
      width: 140px;
      padding: 9px 32px;
      color: black;
    }
  }
}

</style>

<style lang="scss" scoped>
.contest {

  display: flex;
  flex-direction: column;
  background-color: white;
  gap: 25px;
  margin: 15px;
  padding: 30px;
  box-shadow: 0px 0px 28px rgb(0 0 0 / 5%);

  .row {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    height: 56px;

    &.multiple-inputs {
      height: 100%;
    }

    .multiple-inputs-container {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      gap: 20px;
      flex-wrap: wrap;
    }

    &.text {
      font-weight: 500;
    }

    .btn {
      width: 140px;
      height: 30px;
    }

    .control {
      display: flex;
      gap: 15px;
      height: 33px;

      .color {
        width: 100px;

        &-box {
          width: 90px;
          background-color: black;
          border-radius: 4px;
        }
      }

      .input-long {
        max-width: 450px;
        width: 100%;
      }

      .input-short {
        max-width: 250px;
        width: 100%;
      }

      .select {
        width: 135px;
      }
    }
  }
}
</style>
