export default function (http) {
    return {
        getSectionTemplates() {
            return new Promise((res, rej) => {
                http.post('manager-section-templates/list')
                    .then((data) => {
                        res(data.data)
                    })
                    .catch(err => rej(err))
            });
        },
        getPageTemplates() {
            return new Promise((res, rej) => {
                http.post('manager-page-templates/list')
                    .then((data) => {
                        res(data.data)
                    })
                    .catch(err => rej(err))
            });
        },
        searchUser(user) {
            return new Promise((res, rej) => {
                http.post('users-manager/search-user', user)
                    .then((data) => {
                        res(data.data)
                    })
                    .catch(err => rej(err))
            });
        },
        addUser(user) {
            return new Promise((res, rej) => {
                http.post('users-manager/add-user', user)
                    .then((data) => {
                        res(data.data)
                    })
                    .catch(err => rej(err))
            });
        },
        setDomains(id, num) {
            return new Promise((res, rej) => {
                http.post('users-manager/change-wp-domains', {id, num})
                    .then((data) => {
                        res(data.data)
                    })
                    .catch(err => rej(err))
            });
        },
        enable(userId, feature) {
            const featureEndpoints = {
                extraFeatures: 'enable-extra-features',
                monthlyTemplates: 'enable-monthly-templates',
                downsellTemplates: 'enable-templates',
                reloadedUpgrade: 'enable-reloaded-upgrade',
            }
            const endpoint = featureEndpoints[feature]

            return new Promise((res, rej) => {
                http.post(`users-manager/${endpoint}`, {id: userId})
                    .then((data) => {
                        res(data.data)
                    })
                    .catch(err => rej(err))
            });
        },
        disable(userId, feature) {
            const featureEndpoints = {
                extraFeatures: 'disable-extra-features',
                monthlyTemplates: 'disable-monthly-templates',
                downsellTemplates: 'disable-templates',
                reloadedUpgrade: 'disable-reloaded-upgrade',
            }
            const endpoint = featureEndpoints[feature]

            return new Promise((res, rej) => {
                http.post(`users-manager/${endpoint}`, {id: userId})
                    .then((data) => {
                        res(data.data)
                    })
                    .catch(err => rej(err))
            });
        },
        disableUser(id) {
            return new Promise((res, rej) => {
                http.post('users-manager/disable-user', {id})
                    .then((data) => {
                        res(data.data)
                    })
                    .catch(err => rej(err))
            });
        },
        enableUser(id) {
            return new Promise((res, rej) => {
                http.post('users-manager/reenable-user', {id})
                    .then((data) => {
                        res(data.data)
                    })
                    .catch(err => rej(err))
            });
        },
        toggleYouzign(id, flag) {
            return new Promise((res, rej) => {
                http.post('users-manager/update-youzign-flag', {id, flag})
                    .then((data) => {
                        res(data.data)
                    })
                    .catch(err => rej(err))
            });
        },
        toggleMembership(id, flag) {
            return new Promise((res, rej) => {
                http.post('users-manager/update-membership-flag', {id, flag})
                    .then((data) => {
                        res(data.data)
                    })
                    .catch(err => rej(err))
            });
        },
        toggleContests(id, flag) {
            return new Promise((res, rej) => {
                http.post('users-manager/update-disable-contests-flag', {id, flag})
                    .then((data) => {
                        res(data.data)
                    })
                    .catch(err => rej(err))
            });
        },
        removeTrial(id) {
            return new Promise((res, rej) => {
                http.post('users-manager/remove-trial', {id})
                    .then((data) => {
                        res(data.data)
                    })
                    .catch(err => rej(err))
            });
        },
        saveTemplate(template, index) {
            const f = new FormData()
            const flattenedSettings = convertJSONAssoc({ template })

            flattenedSettings.forEach((el) => {
                let newKey = el.key.replace('[', '')
                newKey = newKey.replace(']', '')
                f.append(newKey, el.val)
            })
            f.append('position', index)

            return new Promise((res, rej) => {
                http.post('manager-page-templates/save', f)
                    .then((data) => {
                        res(data.data)
                    })
                    .catch(err => rej(err))
            });
        },
        saveSections(templates) {
            const f = new FormData()
            const flattenedSettings = convertJSONAssoc({ templates })

            flattenedSettings.forEach((el) => {
                let newKey = el.key.replace('[', '')
                newKey = newKey.replace(']', '')
                f.append(newKey, el.val)
            })

            // f.forEach((k, v) => console.log(v, k))

            return new Promise((res, rej) => {
                http.post('manager-section-templates/save', f)
                    .then((data) => {
                        res(data.data)
                    })
                    .catch(err => rej(err))
            });
        },
        refreshThumb(id, code) {
            return new Promise((res, rej) => {
                http.post('page/take-screenshot', {id, code, refresh: true})
                    .then((data) => {
                        res(data.data)
                    })
                    .catch(err => rej(err))
            });
        },
        changePassword(id, password) {
            return new Promise((res, rej) => {
                http.post('users-manager/change-password', {id, password})
                    .then((data) => {
                        res(data.data)
                    })
                    .catch(err => rej(err))
            });
        }
    }
}

/**
 * Formats the JSON result to an associative array, concatenating child and parent keys
 */
function convertJSONAssoc(obj, firstlevel) {
    // consider string, number and boolean values in JSON as the last
    // elements and can't be recursed into any further
    if (typeof obj == 'string' || typeof obj == 'number' || typeof obj == 'boolean') {
        var ab = [];
        var bc = {};
        bc.key = '';
        bc.val = obj;
        ab.push(bc);
        return ab;
    }

    // the top most call which builds the final result
    if (firstlevel) {
        var result = {};
        for (key in obj) {
            var val = obj[key];
            var s = convertJSONAssoc(val, false);
            for (var o = 0; o < s.length; o++) {
                var v = s[o];
                result[key + v['key']] = v['val'];
            }
        }
        return result;
    } else {
        // this is where the recursion happens. As long as objects are found,
        // we use the code below to keep on parsing obj keys
        var paths = [];
        for (var key in obj) {
            var val = obj[key];
            var s = convertJSONAssoc(val, false);
            for (var o = 0; o < s.length; o++) {
                var v = s[o];
                var de = {};
                de.key = "[" + key + "]" + v['key'];
                de.val = v['val'];
                paths.push(de);
            }
        }
        return paths;
    }
}
