<template>
  <div class="login">
    <div class="login-form">
      <div class="info">
        Password reset for <br>
        <div class="err">{{ query.get('email') }}</div>
      </div>
      <div class="info red" v-if="error">
        Unexpected error occured. <br>
        Please try again later.
      </div>
      <div class="info" v-else>
        Please type in your new password twice. <br>
        It must have at least 10 characters.
      </div>
      <div class="inputs">
        <el-input
            v-model="newPassword"
            placeholder="New password"
            name="newPassword"
            type="password"
            class="input"/>
        <el-input
            v-model="confirmPassword"
            placeholder="Confirm password"
            name="confirmPassword"
            type="password"
            class="input"/>
      </div>
      <el-button
          :disabled=
              "
          newPassword !== confirmPassword
          || newPassword.length < 10
          || confirmPassword.length < 10
          "
          type="info"
          id="login"
          @click="reset">
        SET NEW PASSWORD
      </el-button>
      <!--      <div class="forget" @click="reset = !reset">-->
      <!--        <ii-lock/>-->
      <!--        Forgot your password?-->
      <!--      </div>-->
    </div>
  </div>
</template>

<script>
export default {
  name: "Reset",
  data: () => ({
    query: new URLSearchParams(),
    newPassword: '',
    confirmPassword: '',
    error: false,
  }),
  created() {
    let params = window.location.href.split('?')
    if (params.length > 1) {
      this.query = new URLSearchParams(params[1])
    }
  },
  methods: {
    reset() {
      this.$api.user.setNewPassword(
          this.query.get('email'),
          this.query.get('code'),
          this.newPassword
    )
    .then(
      window.location.href = 'https://launch.iglooapp.com'
    )
    .catch(
      this.error = true
    )
    },
  }
}
</script>

<style lang="scss">
.login {
  background-image: url("~@/assets/login_bg.jpg");

  .el-input__inner {
    width: 100%;
    height: 50px !important;
    background-color: white;
    text-align: center;
  }
}
</style>

<style lang="scss" scoped>
.login {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  .err {
    text-align: center;
    font-size: 16px;
    font-weight: 700;
    color: $clr-green;
  }

  &-form {
    background-color: $clr-primary;
    border-radius: 15px;
    //margin: auto 30px;
    width: 550px;
    height: 420px;
    padding: 30px 50px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    gap: 25px;

    .forget {
      color: #c5d6de;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;

      &:hover {
        color: white;
      }

      svg {
        width: 20px;
        height: 20px;
      }
    }

    .logo {

    }

    .info {
      font-size: 18px;
      color: white;
      text-align: center;

      &.red {
        color: $clr-red;
      }
    }

    .inputs {
      width: 100%;
      display: flex;
      flex-direction: column;
      gap: 20px;
    }

    #login, #instructions {
      width: 100%;
      height: 50px;

      font-size: 20px;
      font-weight: 700;
    }
  }
}
</style>