<template>
  <el-dialog
      class="new-campaign-modal"
      :before-close="handleClose"
      :visible.sync="value"
      :append-to-body="true">
    <el-container>
      <span class="title">Create a new campaign</span>
    </el-container>
    <el-input v-model="form.campaignName" placeholder="Enter new campaign name..."></el-input>
    <span slot="footer" class="dialog-footer">
    <el-button
        type="primary"
        @click="handleCreate"
        :disabled="form.campaignName.length == 0"
        id="modal-create-campaign">Create</el-button>
    <el-button @click="handleClose">Close</el-button>
  </span>
  </el-dialog>
</template>

<script>
import {mapActions} from "vuex";

export default {
  props: {
    value: {type: Boolean, required: true,}
  },
  methods: {
    ...mapActions(['createCampaign']),
    handleClose() {
      this.$emit('input', false);
    },
    handleCreate() {
      this.createCampaign(this.form.campaignName).then((id) => {
        this.$router.push(`/campaign/${id}`);
        this.handleClose();
      })
    }
  },
  data: () => ({
    form: {
      campaignName: '',
    }
  })
};
</script>

<style lang="scss">
.new-campaign-modal {
  width: 100% !important;
  display: flex;
  align-items: center;
  justify-content: center;

  #modal-create-campaign {

    &:hover {

    }
  }

  .el-dialog {
    background-color: $clr-bg-light;
    border-radius: 15px;
    margin: auto 30px;
    width: 100%;
    margin-top: auto !important;

    &__body {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    .el-input {
      display: flex;
      justify-content: center;

      &__inner {
        width: 400px;
        height: 45px;
        border: none;
        outline: none;
        border-radius: 15px;
        text-align: center;
      }
    }

    &__wrapper {
      width: 100% !important;
    }

    @media (max-width: $media-tablet) {
      max-width: 490px;

      input {

        width: 260px;
      }
    }

    @media (min-width: $media-tablet) {
      width: 650px;

      input {

        width: 350px;
      }
    }

    @media (max-width: 470px) {
      max-width: 340px;

      input {

        width: 260px;
      }
    }

    &__title {
      display: flex;
    }

    &__body {
      padding-bottom: 25px;

      .title {
        font-size: 24px;
        margin: auto auto 35px;
      }
    }

    &__footer {
      display: flex;
      justify-content: center;
    }
  }
}
</style>

<style lang="scss" scoped>

</style>
