<template>
  <div class="campaigns" v-if="user">
    <el-row type="flex" :gutter="50" style="flex-wrap: wrap; margin-bottom: 25px;">
      <el-col :span="24" class="builder-select">
        <span class="builder" :class="{'active': !newBuilder}" @click="newBuilder = false">old builder</span>
        /
        <span class="builder" :class="{'active': newBuilder}" @click="newBuilder = true">new builder</span>
      </el-col>
      <el-col :span="24" class="filters">
<!-- <Team/> -->
        <div class="filters-mobile">
          <el-select placeholder="Filters" v-model="selectedFilter">
            <el-option v-for="f in filters" :key="f.title" :value="f.category">
              {{ f.title }}
            </el-option>
          </el-select>
        </div>
        <div class="filters__inner">
          <div v-for="f in filters"
               :key="f.title"
               :class="{
                 'active': selectedFilter == f.category,
                 'hidden': f.category == 'mytemplates' && !(user.user.settings.page_templates)
               }"
               class="filter"
               @click="select(f)">
            <component :is="f.icon"/>
            {{ f.title }}
          </div>
        </div>
      </el-col>
    </el-row>
    <div class="cards">
      <template v-for="t in templates">
        <div v-if="!t.hideThumb" class="card" :key="t.id">
          <img :src="t.thumb" @error="test($event, t)">
          <div class="cover">
            <div class="preview">
              <ii-preview/>
            </div>
            <div class="select" @click="handleSelectTemplate(t)">
              <ii-select/>
            </div>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import Team from "@/components/Team";
import IconMail from '@/components/icons/Mail';
import IconCart from '@/components/icons/Cart';
import IconLock from '@/components/icons/Lock';
import IconPresentation from '@/components/icons/Presentation';
import IconChart from '@/components/icons/Chart';
import IconProfile from '@/components/icons/Profile';
import IconDownload from '@/components/icons/Download';
import IconCup from '@/components/icons/Cup';
import IconGeo from '@/components/icons/Geo';
import IconBlank from '@/components/icons/Blank';
import {mapActions, mapGetters} from "vuex";

export default {
  name: "SelectTemplate",
  components: {Team},
  data: () => ({
    filters: [
      {icon: IconMail, title: 'Optin', category: 'optin'},
      {icon: IconCart, title: 'Landing', category: 'landing'},
      {icon: IconLock, title: 'Membership', category: 'membership'},
      {icon: IconPresentation, title: 'Webinar', category: 'webinar'},
      {icon: IconChart, title: 'Upsell', category: 'upsell'},
      {icon: IconProfile, title: 'Affiliate', category: 'affiliate'},
      {icon: IconDownload, title: 'Download', category: 'download'},
      {icon: IconCup, title: 'Pre Launch', category: 'pre-launch'},
      {icon: IconGeo, title: 'My Templates', category: 'mytemplates'},
      {icon: IconBlank, title: 'Blank', category: 'blank'},
    ],
    selectedFilter: 'optin',
    newBuilder: false,
  }),
  methods: {
    ...mapActions(['setTemplate']),
    handleSelectTemplate(template) {
      this.setTemplate({template, newEditor: this.newBuilder})
    },
    select(f) {
      // if(f.category == 'blank') {
      //   this.setTemplate({id: null}, true)
      // }
      this.selectedFilter = f.category
    },
    test(e, t) {
      if (e.target) {
        if (!e.target.naturalWidth || !e.target.naturalHeight) {
          this.$set(t, 'hideThumb', true)
        }
      }
    }
  },
  computed: {
    ...mapGetters(['pages', 'pagesOld', 'currentCampaign', 'user', 'currentPage']),
    templates() {
      return this.newBuilder
          ? this.pages(this.selectedFilter)
          : this.pagesOld(this.selectedFilter)
    },
  },
  watch: {
    selectedFilter: {
      deep: true,
      immediate: true,
      handler(f) {
        if (f == 'blank') {
          this.setTemplate({template: {id: null}, newEditor: this.newBuilder})
        }
      }
    }
  }
}

</script>

<style lang="scss">
.filters-mobile {
  .el-input {
    border-radius: 10px !important;
    font-size: 12px;

    i {
      line-height: 25px;
    }

    &__inner {
      background-color: transparent;
      width: 130px;
      height: 25px;
      border: 1px solid $clr-grey !important;
      border-radius: 10px !important;
    }
  }
}
</style>

<style lang="scss" scoped>

.campaigns {
  width: 100%;

  @media (min-width: 1360px) {
    width: 1150px;
  }

  @media (min-width: 1630px) {
    width: 1350px;
  }

  .el-row {
    padding: 0 25px;
  }
}

.cards {
  display: flex;
  gap: 30px;
  flex-wrap: wrap;
  justify-content: flex-start;

  @media (max-width: $media-tablet) {
    padding: 30px;
    gap: 16px;
  }
}

.card {
  display: flex;
  background-color: $clr-bg-light;
  width: 335px;
  height: 370px;
  position: relative;
  border-radius: 8px;

  margin: auto;

  @media (max-width: $media-mobile) {
    //width: 270px;
    //height: 300px;
  }

  @media (max-width: $media-tablet) and (min-width: $media-mobile) {
    width: 210px;
    height: 240px;
  }

  @media (min-width: $media-tablet) {
    margin: 0;
  }


  img {
    max-width: 100%;
    max-height: 100%;
    object-fit: cover;
    border-radius: 8px;

    width: 100%;
  }

  .cover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000000;
    opacity: 0;
    transition: all 0.4s ease;
    border-radius: 8px;

    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
      opacity: 0.8;
    }

    .preview {
      color: $clr-primary;
      margin-right: 36px;
      transition: all ease 0.3s;
      cursor: pointer;

      &:hover {
        transform: scale(1.06) rotate(10deg);
      }
    }

    .select {
      color: $clr-green;
      transition: all ease 0.3s;
      cursor: pointer;

      &:hover {
        transform: scale(1.06) rotate(10deg);
      }
    }
  }
}

.builder-select {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  gap: 5px;

  .builder {
    font-size: 16px;
    cursor: pointer;

    &.active {
      font-weight: 700;
    }
  }
}

.filters {
  display: flex;
  align-items: flex-start;
  font-size: 10px;
  font-weight: 500;

  #team {
    margin-right: 15px;
  }

  &-mobile {
    display: none;
    margin-left: auto;
  }

  &__inner {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: flex-start;
    height: 100%;
    flex-direction: row;
    flex: 1;
  }

  .filter {
    height: 16px;
    margin: 6px 25px 6px 0;
    display: flex;
    align-items: center;
    color: $clr-txt-topbar;
    transition: all 0.2s;

    cursor: pointer;

    &:hover, &.active {
      color: $clr-primary-light;
    }

    &.hidden {
      display: none;
    }

    svg {
      margin-right: 7px;
    }
  }
}

@media (max-width: $media-desktop) {
  .filter {
    margin-right: 10px !important;
  }
}

@media (max-width: 670px) {
  .filters {
    align-items: center;

    &__inner {
      display: none;
    }

    &-mobile {
      display: block;
    }
  }
}

</style>
