<template>
  <el-dialog
      class="delete-campaign-modal"
      :before-close="handleClose"
      :visible.sync="value"
      :append-to-body="true">
    <el-container>
      <span class="title">Are you sure to delete this campaign?</span>
    </el-container>
    <el-container>
      <span class="name">
      {{ deleteName }}
      </span>
    </el-container>
    <span slot="footer" class="dialog-footer">
    <el-button
        type="primary"
        @click="handleDelete"
        id="modal-create-campaign">Delete</el-button>
    <el-button @click="handleClose">Close</el-button>
  </span>
  </el-dialog>
</template>

<script>
import {mapActions} from "vuex";

export default {
  props: {
    value: {type: Boolean, required: true,},
    deleteId: {type: [Number, String], required: true,},
    deleteName: {type: String, required: true,},
  },
  methods: {
    ...mapActions(['deleteCampaign']),
    handleClose() {
      this.$emit('input', false);
    },
    handleDelete() {
      this.deleteCampaign(this.deleteId).then(() => {
        this.handleClose();
      })
    }
  },
  data: () => ({
    contestName: '',
  })
};
</script>

<style lang="scss">
.delete-campaign-modal {
  width: 100% !important;
  display: flex;
  align-items: center;
  justify-content: center;

  #modal-create-campaign {

    &:hover {

    }
  }

  .el-dialog {
    background-color: $clr-bg-light;
    border-radius: 15px;
    margin: auto 30px;
    width: 100%;
    margin-top: auto !important;

    &__body {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    .el-input {
      display: flex;
      justify-content: center;

      &__inner {
        width: 400px;
        height: 45px;
        border: none;
        outline: none;
        border-radius: 15px;
        text-align: center;
      }
    }

    &__wrapper {
      width: 100% !important;
    }

    @media (max-width: $media-tablet) {
      max-width: 490px;

      input {

        width: 260px;
      }
    }

    @media (min-width: $media-tablet) {
      width: 650px;

      input {

        width: 350px;
      }
    }

    @media (max-width: 470px) {
      max-width: 340px;

      input {

        width: 260px;
      }
    }

    &__title {
      display: flex;
    }

    &__body {
      padding-bottom: 25px;

      .title {
        font-size: 24px;
        margin: auto auto 35px;
      }

      .name {
        font-size: 20px;
        margin: auto auto 35px;
      }
    }

    &__footer {
      display: flex;
      justify-content: center;
    }
  }
}
</style>

