<template>
  <div class="page-settings">
    <Subscribers @toggle-settings="$emit('toggle-settings')" @toggle-subscribers="$emit('toggle-subscribers')"/>
    <div class="card">
      <div class="row">
        Funnel Name
        <div class="control">
          <el-input v-model="settings.name"/>
        </div>
      </div>
      <div class="row">
        Enable Site
        <div class="control">
          <el-checkbox v-model="settings.enabled"></el-checkbox>
        </div>
      </div>
      <div class="row" v-if="settings.enabled">
        Home Page
        <div class="control">
          <el-select v-model="settings.homepage">
            <el-option v-for="(s, index) in currentCampaign.settings.steps" :value="s" :label="s.name"
                       :key="index"></el-option>
          </el-select>
        </div>
      </div>
      <div class="row" v-if="settings.enabled">
        Domain Address
        <div class="control">
          <el-input v-model="settings.address"></el-input>
        </div>
      </div>
      <div class="row">
        <div class="control">
          <el-button
              type="primary"
              @click="handleSaveGeneral"
              :disabled="!(settings.name && settings.enabled && settings.homepage && settings.address)">
            Update Settings
          </el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Subscribers from "@/components/Subscribers";
import {mapActions, mapGetters} from "vuex";

export default {
  components: {Subscribers},
  name: "PageSettings",
  data: () => ({
    settings: {
      name: '',
      enabled: false,
      homepage: '',
      address: '',
    }
  }),
  methods: {
    ...mapActions(['saveCampaign', 'loadCampaignSettings']),
    handleSaveGeneral() {
      this.saveCampaign({
        name: this.settings.name,
        domain: this.settings.address,
        home_page_id: this.settings.homepage.page.id,
        out_site: this.settings.enabled ? 1 : 0
      })
    }
  },
  computed: {
    ...mapGetters(['currentCampaign', 'currentPage', 'campaignSettings', 'pageById'])
  },
  watch: {
    currentCampaign: {
      handler(c) {
        if (c) {
          this.loadCampaignSettings(c.id).then(() => {
            this.$nextTick(() => {
              this.settings.name = this.currentCampaign.name
              this.settings.enabled = this.campaignSettings.out_site == 1 ? true : false
              this.settings.address = this.campaignSettings.domain || `${this.currentCampaign.name}${this.currentCampaign.id}.igloopage.com`
              this.settings.homepage = this.pageById(this.campaignSettings.home_page_id) || ''
              console.log(this.pageById(this.campaignSettings.home_page_id))
            });
          });
        }
      },
      deep: true,
      immediate: true,
    }
  }
}
</script>

<style lang="scss">
.page-settings {
  .el-button {
    padding: 4px 10px;
  }

  .el-input, .el-select {

    &__inner {
      width: 270px !important;
      height: 33px !important;
      line-height: 33px !important;
    }

    .el-icon-arrow {
      &-up, &-down {
        line-height: 33px !important;
      }
    }
  }
}
</style>

<style lang="scss" scoped>
.page-settings {
  position: relative;
  width: 100%;
  padding: 0 30px;
  font-weight: 500;
  font-size: 16px;

  .card {
    display: flex;
    flex-direction: column;
    gap: 8px;

    .row {
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 100%;
      height: 60px;
      gap: 4px;

      .control {
        display: flex;
        gap: 15px;
        height: 33px;

        .color {
          width: 100px;

          &-box {
            width: 90px;
            background-color: black;
            border-radius: 4px;
          }
        }
      }
    }
  }

  .subscribe {
    position: absolute;
    right: 40px;
    width: 190px;
    height: 30px;

    display: flex;
    justify-content: space-between;

    svg {
      margin-right: 10px;
    }
  }

  .card {
    margin-top: 45px;
    position: relative;
    background-color: white;
    box-shadow: 0px 0px 28px rgba(0, 0, 0, 0.05);
    padding: 25px;
    width: 100%;

    &#templates {
      padding: 0;
    }

    @media (max-width: $media-mobile) {
      &#templates {
        margin-top: 0;
      }
    }
  }
}
</style>
