/* eslint-disable no-redeclare */
export default function (http) {
    return {
        getMe() {
            return new Promise((res, rej) => {
                http.post('user/autologin')
                    .then(data => res(data.data))
                    .catch(err => rej(err))
            });
        },
        uploadAvatar(avatarData) {
            const f = new FormData()
            const flattenedSettings = convertJSONAssoc(avatarData)

            flattenedSettings.forEach((el) => {
                let newKey = el.key.replace('[', '')
                newKey = newKey.replace(']', '')
                f.append(newKey, el.val)
            })

            return new Promise((res, rej) => {
                http.post('images/upload-one', f)
                    .then(data => res(data.data))
                    .catch(err => rej(err))
            });
        },
        getSettings() {
            return new Promise((res, rej) => {
                http.post('user/get-settings')
                    .then(data => res(data.data))
                    .catch(err => rej(err))
            });
        },
        getDomains() {
            return new Promise((res, rej) => {
                http.post('wp/domains')
                    .then(data => res(data.data))
                    .catch(err => rej(err))
            });
        },
        login(email, password) {
            return new Promise((res, rej) => {
                http.post('user/login', {email, password})
                    .then(data => res(data.data))
                    .catch(err => rej(err))
            });
        },
        resetPassword(email) {
            return new Promise((res, rej) => {
                http.post('user/lost-password', {email})
                    .then(data => res(data.data))
                    .catch(err => rej(err))
            });
        },
        setNewPassword(email, code, password) {
            return new Promise((res, rej) => {
                http.post('user/reset-password', {email, code, password})
                    .then(data => res(data.data))
                    .catch(err => rej(err))
            });
        },
        changePassword(password) {
            return new Promise((res, rej) => {
                http.post('user/change-password', {password})
                    .then(data => res(data.data))
                    .catch(err => rej(err))
            });
        },
        saveUsername(username) {
            return new Promise((res, rej) => {
                http.post('user/save-username', {username})
                    .then(data => res(data.data))
                    .catch(err => rej(err))
            });
        },
        saveSettings(settings) {
            const f = new FormData()
            const flattenedSettings = convertJSONAssoc(settings)

            flattenedSettings.forEach((el) => {
                let newKey = el.key.replace('[', '')
                newKey = newKey.replace(']', '')
                f.append(newKey, el.val)
            })

            return new Promise((res, rej) => {
                http.post('user/save-settings', f)
                    .then(data => res(data.data))
                    .catch(err => rej(err))
            });
        },
        connectYouZign(youZignSettings) {
            return new Promise((res, rej) => {
                http.post('youzign/connect', {apikey: youZignSettings.apikey, tk: youZignSettings.token})
                    .then(data => res(data.data))
                    .catch(err => rej(err))
            });
        },
        getMyTemplates() {
            return new Promise((res, rej) => {
                http.post('user-page-templates/list')
                    .then(data => res(data.data))
                    .catch(err => rej(err))
            });
        }
    }
}

/**
 * Formats the JSON result to an associative array, concatenating child and parent keys
 */
function convertJSONAssoc(obj, firstlevel) {
    // consider string, number and boolean values in JSON as the last
    // elements and can't be recursed into any further
    if (typeof obj == 'string' || typeof obj == 'number' || typeof obj == 'boolean') {
        var ab = [];
        var bc = {};
        bc.key = '';
        bc.val = obj;
        ab.push(bc);
        return ab;
    }

    // the top most call which builds the final result
    if (firstlevel) {
        var result = {};
        for (key in obj) {
            var val = obj[key];
            var s = convertJSONAssoc(val, false);
            for (var o = 0; o < s.length; o++) {
                var v = s[o];
                result[key + v['key']] = v['val'];
            }
        }
        return result;
    } else {
        // this is where the recursion happens. As long as objects are found,
        // we use the code below to keep on parsing obj keys
        var paths = [];
        for (var key in obj) {
            var val = obj[key];
            var s = convertJSONAssoc(val, false);
            for (var o = 0; o < s.length; o++) {
                var v = s[o];
                var de = {};
                de.key = "[" + key + "]" + v['key'];
                de.val = v['val'];
                paths.push(de);
            }
        }
        return paths;
    }
}
