export default function (http) {
    return {
        addAutoResponder(settings) {
            const f = new FormData();
            Object.entries(settings).forEach(el => f.append(`settings[${el[0]}]`, el[1]))

            return new Promise((res, rej) => {
                http.post('autoresponder/add', f) // {"settings": {...settings}}
                    .then(data => res(data.data))
                    .catch(err => rej(err))
            })
        },
        getEmails(campaignId) {
            return new Promise((res, rej) => {
                http.post('autoresponder/get-emails', {id_campaign: campaignId})
                    .then(data => res(data.data))
                    .catch(err => rej(err))
            })
        },
    }
}
