<template>
  <Login v-if="!query.has('reset')"/>
  <Reset v-else/>
</template>

<script>
import Login from "@/components/Login";
import Reset from "@/components/Reset";

export default {
  components: { Login, Reset },
  name: "LoginPage",
  data: () => ({
    query: new URLSearchParams(),
  }),
  created() {
    let params = window.location.href.split('?')
    if (params.length > 1) {
      this.query = new URLSearchParams(params[1])
    }
  }
}
</script>

