import Vue from 'vue';
import VueRouter from 'vue-router';
import Dashboard from '../views/Dashboard.vue';
import Campaign from "@/views/Campaign";
import CampaignsSearch from "@/views/CampaignsSearch";
import Settings from "@/views/Settings";
import AppLayout from "@/components/layouts/AppLayout";
import SettingsLayout from "@/components/layouts/SettingsLayout";
import Contest from "@/views/Contest";

import ContestSpecialSidebar from "@/components/ContestSpecialSidebar";
import LoginPage from "@/views/LoginPage";
import LoginLayout from "@/components/layouts/LoginLayout";
import Templates from "@/views/admin/Templates";
import Sections from "@/views/admin/Sections";
import Users from "@/views/admin/Users";

Vue.use(VueRouter);

const routes = [
    {
        path: '/',
        component: AppLayout,
        children: [
            {
                path: '/',
                name: 'Dashboard',
                component: Dashboard,
            },
            {
                path: '/adm-templates',
                name: 'Templates',
                component: Templates,
            },
            {
                path: '/adm-sections',
                name: 'Sections',
                component: Sections,
            },
            {
                path: '/adm-users',
                name: 'Users',
                component: Users,
            },
            {
                path: '/campaign/:id',
                name: 'Campaign',
                component: Campaign,
                meta: {
                    sidebar: true,
                }
            },
            {
                path: '/campaigns',
                name: 'Campaigns',
                component: CampaignsSearch,
            },
            {
                path: '/contest',
                name: 'Contest',
                component: Contest,
                meta: {
                    sidebar: ContestSpecialSidebar,
                }
            },
        ],
    },
    {
        path: '/',
        component: SettingsLayout,
        children: [
            {
                path: '/settings',
                name: 'Settings',
                component: Settings,
                meta: {
                    specialSidebar: true,
                }
            },
        ]
    },
    {
        path: '/',
        component: LoginLayout,
        children: [
            {
                path: '/login',
                name: 'Login',
                component: LoginPage
            }
        ]
    }
];

const router = new VueRouter({
    mode: 'hash',
    base: process.env.BASE_URL,
    linkActiveClass: 'active',
    routes,
});

export default router;
