<template>
  <el-dialog
      class="delete-modal"
      :before-close="handleClose"
      :visible.sync="value"
      :append-to-body="true">
    <el-container>
      <span class="title">Are you sure to delete this page?</span>
    </el-container>
    <el-container>
      <span class="name">
      {{ stepName }}
      </span>
    </el-container>
    <span slot="footer" class="dialog-footer">
    <el-button
        type="primary"
        @click="handleDelete"
        id="modal-delete">Delete</el-button>
    <el-button @click="handleClose">Cancel</el-button>
  </span>
  </el-dialog>
</template>

<script>
import {mapActions, mapGetters} from "vuex";

export default {
  props: {
    value: {type: Boolean, required: true,},
    stepId: {type: [String, Number], required: true},
    stepName: {type: String, required: true}
  },
  methods: {
    ...mapActions(['deleteStep']),
    handleClose() {
      this.$emit('input', false);
    },
    handleDelete() {
      const toDelete = this.stepId // || this.currentCampaignPage
      this.deleteStep(toDelete)
      this.handleClose()
      console.log('deleted ' + toDelete)
    }
  },
  computed: {
    ...mapGetters(['currentCampaignPage'])
  }
};
</script>

<style lang="scss">
.delete-modal {
  width: 100% !important;
  display: flex;
  align-items: center;
  justify-content: center;

  #modal-delete {

    &:hover {

    }
  }

  .el-dialog {
    background-color: $clr-bg-light;
    border-radius: 15px;
    margin: auto 30px;
    width: 100%;
    margin-top: auto !important;

    &__body {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    .el-input {
      display: flex;
      justify-content: center;

      &__inner {
        width: 400px;
        height: 45px;
        border: none;
        outline: none;
        border-radius: 15px;
        text-align: center;
      }
    }

    &__wrapper {
      width: 100% !important;
    }

    @media (max-width: $media-tablet) {
      max-width: 490px;

      input {

        width: 260px;
      }
    }

    @media (min-width: $media-tablet) {
      width: 650px;

      input {

        width: 350px;
      }
    }

    @media (max-width: 470px) {
      max-width: 340px;

      input {

        width: 260px;
      }
    }

    &__title {
      display: flex;
    }

    &__body {
      padding-bottom: 25px;

      .title {
        font-size: 24px;
        margin: auto auto 35px;
      }

      .name {
        font-size: 20px;
        margin: auto auto 35px;
      }
    }

    &__footer {
      display: flex;
      justify-content: center;
    }
  }
}
</style>
