export default function (http) {
    return {
        getAllContests() {
            return new Promise((res, rej) => {
                http.post('contest/list')
                    .then((data) => {
                        let contests = data.data;
                        // if (contests && contests.length > 0) {
                        //     contests = contests.map(c => ({...c, settings: JSON.parse(c.settings)}));
                        // }
                        res(contests);
                    })
                    .catch(err => rej(err))
            });
        },
        createNewContest(contestName) {
            return new Promise((res, rej) => {
                let contest = {
                    contest: {
                        name: contestName,
                        settings: {
                            color: "#444",
                            bgColor: "#ECE7C9",
                            iconColor: "#39bed7",
                            iconPosition: "left",
                            share_description: "Thanks for subscribing. Share your unique referral link to get points to win prizes..",
                            points_description: "You've collected",
                            rank_description: "Your Rank is",
                            youtube_url: "",
                            youtube_code: "",
                            end_date: false,
                            date: "",
                            hours: "11",
                            minutes: "00",
                            timezone: "-5"
                        }
                    }
                }
                http.post('contest/create', contest)
                    .then(data => res({...contest.contest, ...data.data}))
                    .catch(err => rej(err))
            });
        },
        duplicateContest(contestId) {
            return new Promise((res, rej) => {
                http.post('contest/duplicate', {id: +contestId})
                    .then((data) => {
                        res(data.data)
                    }).catch(err => rej(err))
            });
        },
        deleteContest(contestId) {
            return new Promise((res, rej) => {
                http.post('contest/delete', {id: +contestId})
                    .then(data => res(data.data))
                    .catch(err => rej(err))
            })
        },
        saveContest(contest) {
            const parsedContest = {
                id: +contest.id,
                contest: {
                    ...contest
                }
            }
            return new Promise((res, rej) => {
                http.post('contest/save', parsedContest)
                    .then(data => res(data.data))
                    .catch(err => rej(err))
            })
        }
    }
}
