<template>
  <div class="account">
    <ChangePasswordModal v-model="changePasswordVisible"/>
    <!--    <el-upload-->
    <!--        action=""-->
    <!--        :auto-upload="false"-->
    <!--        :show-file-list="false"-->
    <!--        :on-change="onProfilePicUpload"-->
    <!--        :limit="1"-->
    <!--        accept="image/png, image/jpeg">-->
    <ProfilePic @click.native="handleAvatar"/>
    <!--    </el-upload>-->
    <ii-key @click.native="changePassword"/>
    <ii-menu class="mobile-only" id="menu" @click.native="openSidebar"/>
<!--    <ii-notifications/>-->
    <!-- TODO mobile logout -->
    <span class="mobile-hidden" id="logout" @click="handleLogout">logout</span>
  </div>
</template>

<script>
import ProfilePic from "@/components/ProfilePic";
import {mapActions} from "vuex";
import ChangePasswordModal from "@/components/ChangePasswordModal";

export default {
  name: "Menu",
  components: {ChangePasswordModal, ProfilePic},
  data: () => ({
    changePasswordVisible: false
  }),
  methods: {
    ...mapActions(['logout', 'uploadProfilePic']),
    handleAvatar() {
      // eslint-disable-next-line no-undef
      cloudinary.openUploadWidget({
        cloud_name: 'dfodbcppd',
        upload_preset: 'igloo-images',
        theme: 'white',
        multiple: false,
        max_files: 1,
        tags: [],
        client_allowed_formats: ["jpg", "png", "gif", "bmp", "tiff", "svg", "webp"],
        max_file_size: null,
        max_image_width: null,
        max_image_height: null,
        folder: 'vidgeos_images',
        form: null,
        sources: ['local', 'url', 'camera'],
        show_powered_by: false,
      }, (err, res) => {
        if (res.event === 'success') {
          this.uploadProfilePic({items: [{...res.info}]})
        }
      })

    },
    openSidebar() {
      this.$bus.$emit('open-sidebar');
    },
    handleLogout() {
      this.logout()
    },
    changePassword() {
      this.changePasswordVisible = true
    },
    onProfilePicUpload(file, list) {
      this.uploadProfilePic(file.raw).then((res) => {
        list.length = 0
        if (res.error) {
          this.importFail = true
        } else {
          console.log('success')
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.account {
  display: flex;
  gap: 15px;

  svg {
    cursor: pointer;

    &:hover {
      color: $clr-primary-hover;
    }
  }
}

#logout {
  cursor: pointer;

  &:hover {
    color: $clr-primary-hover;
  }
}
</style>
