// noinspection EqualityComparisonWithCoercionJS

import Vue from 'vue';
import Vuex from 'vuex';

import * as types from './mutation-types';
import router from "@/router";

Vue.use(Vuex);

export default function (api) {
    const defaultState = {
        campaigns: [],
        campaignsStillLoading: true,
        campaignsLoadCallbacks: [],
        currentCampaign: null,
        currentCampaignPage: null,
        campaignLoading: false,
        contests: [],
        currentContest: null,
        loading: false,
        user: null,
        settings: null,
        domains: [],
        pages: [],
        pagesOld: [],
        emails: [],
        campaignDomainSettings: null,
    };

    return new Vuex.Store({
        state: {...defaultState},
        mutations: {
            [types.RESET_STATE](state) {
                Object.assign(state, {...defaultState})
            },
            [types.SET_LOADING](state, payload) {
                state.loading = payload;
            },
            [types.SET_DOMAIN_SETTINGS](state, payload) {
                state.campaignDomainSettings = payload;
            },
            [types.SET_CAMPAIGN_LOADING](state, payload) {
                state.campaignLoading = payload;
            },
            [types.SET_USER](state, payload) {
                state.user = payload;
            },
            [types.SET_USER_NAME](state, payload) {
                state.user.user.username = payload;
            },
            [types.SET_DOMAINS](state, payload) {
                state.domains = payload;
            },
            [types.SET_YOUZIGN](state, payload) {
                state.settings.youzign = payload;
            },
            [types.SET_EMAILS](state, payload) {
                state.emails = payload;
            },
            [types.SET_CAMPAIGNS](state, payload) {
                state.campaignsStillLoading = false;
                state.campaigns = payload;
            },
            [types.SET_CURRENT_CAMPAIGN](state, payload) {
                state.currentCampaign = payload;
            },
            [types.SET_CURRENT_PAGE](state, payload) {
                state.currentCampaignPage = payload;
            },
            [types.SET_CURRENT_PAGE_TEMPLATE](state, payload) {
                const currentStep = state.currentCampaign.settings.steps[state.currentCampaignPage]
                currentStep.page = payload
            },
            [types.SET_CURRENT_STEP_DEFAULTS_FROM_TEMPLATE](state, payload) {
                const currentStep = state.currentCampaign.settings.steps[state.currentCampaignPage]
                // console.log(payload.newBuilder)
                currentStep.name = payload.template.name
                currentStep.tpl_cat = payload.template.tpl_cat
                currentStep.page.thumb = payload.template.thumb
                currentStep.page.new_builder = payload.newBuilder ? 1 : 0
            },
            [types.SET_CURRENT_STEP_PROPERTY](state, {property, value}) { // name only
                const currentStep = state.currentCampaign.settings.steps[state.currentCampaignPage]
                currentStep[property] = value
            },
            [types.SET_CURRENT_STEP_PAGE_PROPERTY](state, {property, value}) { // footer, header
                const currentStep = state.currentCampaign.settings.steps[state.currentCampaignPage]
                currentStep.page[property] = value
            },
            [types.ADD_STEP](state, payload) {
                if (!state.currentCampaign.settings.steps) {
                    Vue.set(state.currentCampaign.settings, 'steps', [])
                }

                state.currentCampaign.settings.steps.push(payload);
            },
            [types.DELETE_STEP](state, payload) {
                state.currentCampaign.settings.steps.splice(payload, 1);
            },
            [types.DUPLICATE_STEP](state, payload) { // {oldStep: step, newStep: res}
                state.currentCampaign.settings.steps.push({
                    ...payload.oldStep,
                    page: {
                        ...payload.oldStep.page,
                        id: payload.newStep.id,
                        code: payload.newStep.code,
                    }
                });
            },
            [types.DELETE_CAMPAIGN](state, payload) {
                const idx = state.campaigns.findIndex((c) => c.id == payload);
                state.campaigns.splice(idx, 1);
            },
            [types.DUPLICATE_CAMPAIGN](state, payload) {
                const campaign = state.campaigns.find((c) => c.id == payload.id);
                state.campaigns.push({
                    ...campaign,
                    id: payload.newCampaignId
                })
            },
            [types.DUPLICATE_CONTEST](state, payload) {
                const contests = state.contests.find((c) => c.id == payload.id);
                state.contests.push({
                    ...contests,
                    id: payload.newContestId
                })
            },
            [types.SET_PAGES](state, payload) {
                state.pages = payload;
            },
            [types.SET_PAGES_OLD](state, payload) {
                state.pagesOld = payload;
            },
            [types.SET_SETTINGS](state, payload) {
                // state.settings = payload;
                Vue.set(state, 'settings', payload);
            },
            [types.ADD_CAMPAIGN_CALLBACK](state, payload) {
                state.campaignsLoadCallbacks.push(payload);
            },
            [types.RESOLVE_CAMPAIGN_CALLBACKS](state) {
                state.campaignsLoadCallbacks.forEach((cb) => {
                    cb();
                });

                state.campaignsLoadCallbacks = [];
            },
            [types.ADD_CAMPAIGN](state, payload) {
                state.campaigns.push(payload);
            },
            [types.SET_CONTESTS](state, payload) {
                state.contests = payload;
            },
            [types.ADD_CONTEST](state, payload) {
                state.contests.push(payload);
            },
            [types.SET_CURRENT_CONTEST](state, payload) {
                state.currentContest = payload;
            },
            [types.DELETE_CONTEST](state, payload) {
                const idx = state.contests.findIndex((c) => c.id == payload);
                state.contests.splice(idx, 1);
            },
            [types.DELETE_AUTORESPONDER](state, payload) {
                const idx = state.settings.autoresponders.findIndex((c) => c.id == payload);
                state.settings.autoresponders.splice(idx, 1);
            },
        },
        getters: {
            user: state => state.user,
            domains: state => state.domains,
            isLoading: state => state.loading,
            campaigns: state => state.campaigns,
            currentCampaign: state => state.currentCampaign,
            currentPage: (state) => {
                if (state.currentCampaign && state.currentCampaign.settings.steps && state.currentCampaignPage !== null) {
                    return state.currentCampaign.settings.steps.find((s, index) => index == state.currentCampaignPage);
                }

                return null;
            },
            currentCampaignPage: state => state.currentCampaignPage,
            pages: (state) => (category = 'optin') => {
                return state.pages[category] || []
            },
            pagesOld: (state) => (category = 'optin') => {
                return state.pagesOld[category] || []
            },
            settings: state => state.settings,
            pageByCode: state => (code) => state.currentCampaign.settings.steps.find((step) => step.page.code == code),
            pageById: state => (id) => {
                return state.currentCampaign.settings.steps.find((step) => step.page.id == id)
            },
            contests: state => state.contests,
            currentContest: state => state.currentContest,
            contestById: state => (contestId) => state.contests.find(c => c.id == contestId),
            emails: state => state.emails,
            campaignSettings: state => state.campaignDomainSettings
        },
        actions: {
            loadUserData({commit}) {
                return api.user.getMe().then((data) => {
                    commit(types.SET_USER, data);
                }).catch((err) => {
                    throw err
                })
            },
            loadUserSettings({commit}) {
                return api.user.getSettings().then((data) => {
                    commit(types.SET_SETTINGS, data);
                }).catch((err) => {
                    throw err
                })
            },
            loadEmails({commit, getters}) {
                return api.autoresponder.getEmails(getters.currentCampaign.id).then((data) => {
                    commit(types.SET_EMAILS, data)
                }).catch()
            },
            login({dispatch}, form) {
                return api.user.login(form.email, form.password)
                    .then((user) => {
                        localStorage.setItem('token', user.token)
                        dispatch('loadDefaultState').then(() => router.push('/'))
                    }).catch((err) => {
                        console.error(err);
                        throw err
                    })
            },
            resetPassword(store, email) {
                return api.user.resetPassword(email)
                    .then((res) => {
                        return res
                    }).catch((err) => {
                        console.error(err);
                        throw err
                    })
            },
            changePassword(store, password) {
                return api.user.changePassword(password)
                    .then((res) => {
                        return res
                    }).catch((err) => {
                        console.error(err);
                        throw err
                    })
            },
            logout({commit}) {
                localStorage.clear()
                commit(types.RESET_STATE)
                router.push('/login')
            },
            uploadProfilePic({getters, dispatch}, data) {
                return api.user.uploadAvatar(data).then((res) => {
                    getters.settings.avatar = res.public_id // TODO getter mutates state
                    dispatch('saveSettings')
                }).catch((err) => {
                    console.error(err);
                    throw err
                })
            },
            takeScreenshot({getters, commit, dispatch}) {
                const currentPage = getters.currentPage.page
                return api.campaign.takeScreenshot({
                    id: currentPage.id,
                    code: currentPage.code,
                    refresh: true
                }).then((res) => {
                    commit(types.SET_CURRENT_STEP_PAGE_PROPERTY, {property: 'thumb', value: res.url})
                    dispatch('saveCampaign')
                })
            },
            async loadCampaign({state, commit}, campaignId) {
                if (state.campaignsStillLoading) {
                    await new Promise((resolve) => {
                        commit(types.ADD_CAMPAIGN_CALLBACK, () => {
                            resolve();
                        });
                    });
                }

                const campaign = state.campaigns.find(c => c.id == campaignId);

                if (campaign) {
                    return Promise.resolve(campaign);
                }

                return api.campaign.getCampaign(campaignId);
            },
            loadCampaignSettings({commit}, campaignId) {
                return api.campaign.getCampaign(campaignId).then((data) => {
                    commit(types.SET_DOMAIN_SETTINGS, data);
                }).catch((err) => {
                    throw err
                })
            },
            loadCampaigns({commit}) {
                return api.campaign.getAllCampaigns().then((data) => {
                    commit(types.SET_CAMPAIGNS, data);
                }).catch((err) => {
                    throw err
                }).finally(() => {
                    commit(types.RESOLVE_CAMPAIGN_CALLBACKS);
                })
            },
            createCampaign({commit}, campaignName) {
                return api.campaign.createCampaign(campaignName).then((data) => {
                    return api.campaign.getCampaign(data.id).then((data) => {
                        commit(types.ADD_CAMPAIGN, data);
                        return data.id;
                    })
                }).catch((err) => {
                    console.error(err)
                    throw err
                })
            },
            exportPage({getters}) {
                return api.campaign.exportPage(getters.currentPage.page.id, getters.currentPage.page.code).then().catch()
            },
            exportPageJson({getters}) { // getters\
                const current = getters.currentPage
                const page = {
                    id: current.page.id,
                    code: current.page.code,
                    name: current.name,
                    thumb: current.page.thumb,
                    // cat: ,
                }
                console.log(page)
                return api.campaign.exportPageJson(page).then().catch() // getters.currentPage.page name code id cat thumb
            },
            deleteCampaign({commit}, campaignId) {
                return api.campaign.deleteCampaign(campaignId).then(() => {
                    commit(types.DELETE_CAMPAIGN, campaignId)
                }).catch((err) => {
                    console.error(err)
                    throw err
                })
            },
            loadDomains({commit}) {
                return api.user.getDomains().then((domainsArray) => {
                    commit(types.SET_DOMAINS, domainsArray)
                }).catch((err) => {
                    console.error(err)
                    throw err
                })
            },
            loadDefaultState({dispatch, commit}) {
                const catchErr = (err) => console.log('err on load default state', err);

                commit(types.SET_LOADING, true);
                const success = dispatch('loadUserData').then(() => {
                    const p = [];

                    p.push(dispatch('loadCampaigns'))
                    p.push(dispatch('loadPages'))
                    p.push(dispatch('loadPagesOld'))
                    p.push(dispatch('loadUserSettings'))
                    p.push(dispatch('loadDomains'))
                    p.push(dispatch('loadContests'))

                    return Promise.all(p)
                }).catch(catchErr);

                if (success) {
                    success.catch(catchErr).finally(() => {
                        commit(types.SET_LOADING, false);
                    })
                }
            },
            setCurrentCampaign({commit, dispatch}, campaignId) {
                dispatch('loadCampaign', campaignId).then(campaign => {
                    if (campaign) {
                        commit(types.SET_CURRENT_CAMPAIGN, campaign)

                        if (campaign.settings.steps) {
                            commit(types.SET_CURRENT_PAGE, 0)
                        }

                    }
                }).catch((err) => {
                    console.error(err)
                    throw err
                })
            },
            setCurrentStep({commit}, stepIndex) {
                commit(types.SET_CURRENT_PAGE, stepIndex)
            },
            addStep({commit, getters, dispatch}) {
                commit(types.ADD_STEP, {name: '- no name -', page: ''})
                api.campaign.saveCampaign(getters.currentCampaign).then(() => {
                    dispatch('setCurrentStep', getters.currentCampaign.settings.steps.length - 1)
                }).catch((err) => {
                    console.error(err)
                    throw err
                })
            },
            // eslint-disable-next-line no-unused-vars
            importStep({commit, dispatch, getters}, zip) {
                return api.campaign.importPage({
                    id: getters.currentCampaign.id,
                    campaign_id: getters.currentCampaign.id,
                    file: zip
                }).then((res) => {
                    if (!res.error) {
                        commit(types.ADD_STEP, {name: res.name, page: res})
                        dispatch('saveCampaign')
                    }
                    return res
                }).catch((err) => {
                    console.error(err)
                    throw err
                })
            },
            deleteStep({commit, getters, dispatch}, index) {
                const numberSteps = getters.currentCampaign.settings.steps.length
                const needToChangeCurrent = getters.currentCampaignPage == numberSteps - 1
                if (needToChangeCurrent) {
                    dispatch('setCurrentStep', (numberSteps - 2) >= 0 ? numberSteps - 2 : 0)
                }
                commit(types.DELETE_STEP, index)
                api.campaign.saveCampaign(getters.currentCampaign).then(() => {}).catch((err) => {
                    console.error(err)
                    throw err
                })
            },
            duplicateStep({commit, dispatch, getters}, stepId) {
                let toCopy = getters.currentPage
                if (stepId || stepId === 0) {
                    toCopy = getters.currentCampaign.settings.steps[stepId]
                }
                api.campaign.duplicateStep(toCopy.page.id).then((res) => {
                    commit(types.DUPLICATE_STEP, {oldStep: toCopy, newStep: res})
                    dispatch('saveCampaign')
                }).catch((err) => {
                    console.error(err)
                    throw err
                })
            },
            loadPages({commit}) {
                let pages
                api.campaign.getPages().then((data) => {
                    pages = data
                    api.user.getMyTemplates().then((myTemplates) => {
                        pages.mytemplates = myTemplates
                    }).catch((err) => {
                        console.error(err)
                        throw err
                    })
                    commit(types.SET_PAGES, pages)
                }).catch((err) => {
                    console.error(err)
                    throw err
                })
            },
            loadPagesOld({commit}) {
                let pages
                api.campaign.getPagesOld().then((data) => {
                    pages = data
                    api.user.getMyTemplates().then((myTemplates) => {
                        pages.mytemplates = myTemplates
                    }).catch((err) => {
                        console.error(err)
                        throw err
                    })
                    commit(types.SET_PAGES_OLD, pages)
                }).catch((err) => {
                    console.error(err)
                    throw err
                })
            },
            loadMyPages({commit}) {
                api.user.getMyTemplates().then((pages) => {
                    commit(types.SET_PAGES, pages)
                }).catch((err) => {
                    console.error(err)
                    throw err
                })
            },
            saveCampaign({getters}, general) {
                if (general) {
                    getters.currentCampaign.general = general
                }
                return api.campaign.saveCampaign(getters.currentCampaign).catch((err) => {
                    console.error(err)
                    throw err
                })
            },
            duplicateCampaign({commit}, campaignId) {
                api.campaign.duplicate(campaignId).then((response) => {
                    commit(types.DUPLICATE_CAMPAIGN, {id: campaignId, newCampaignId: response.id})
                }).catch((err) => {
                    console.error(err)
                    throw err
                })
            },
            duplicateContest({commit}, contestId) {
                api.contest.duplicateContest(contestId).then((response) => {
                    commit(types.DUPLICATE_CONTEST, {id: contestId, newContestId: response.id})
                }).catch((err) => {
                    console.error(err)
                    throw err
                })
            },
            setTemplate({commit, getters, dispatch}, payload) { // blank = false
                const currentCampaign = getters.currentCampaign
                const type = payload.newEditor ? 'new' : 'old'
                api.campaign.setStepTemplate(currentCampaign.id, payload.template.id || '', type).then((page) => {
                    commit(types.SET_CURRENT_PAGE_TEMPLATE, page)
                    commit(types.SET_CURRENT_STEP_DEFAULTS_FROM_TEMPLATE, {
                        newBuilder: payload.newEditor,
                        template: payload.template
                    })
                    dispatch('saveCampaign')
                }).catch((err) => {
                    console.error(err)
                    throw err
                })
            },
            setStepProperty({commit}, {property, value}) {
                if (property == 'name') {
                    commit(types.SET_CURRENT_STEP_PROPERTY, {property, value})
                }

                commit(types.SET_CURRENT_STEP_PAGE_PROPERTY, {property, value})
            },
            saveUsername({commit}, username) {
                return api.user.saveUsername(username).then((res) => {
                    commit(types.SET_USER_NAME, username)
                    return res
                }).catch((err) => {
                    console.error(err)
                    throw err
                })
            },
            addAutoResponder({commit}, settings) {
                return api.autoresponder.addAutoResponder(settings).then((data) => {
                    if ((data.result == 'success' && data.settings)) {
                        commit(types.SET_SETTINGS, data.settings)
                    }
                    return (data.result)
                }).catch((err) => {
                    console.error(err)
                    throw err
                })
            },
            deleteAutoResponder({commit, dispatch}, responderId) {
                commit(types.DELETE_AUTORESPONDER, responderId)
                dispatch('saveSettings')
            },
            addStorage({commit, dispatch, getters}, storage) {
                let settings = {...getters.settings}
                settings.storage = {...storage};
                commit(types.SET_SETTINGS, settings)
                dispatch('saveSettings')
            },
            deleteStorage({commit, dispatch, getters}) {
                let settings = {...getters.settings}
                delete settings.storage
                commit(types.SET_SETTINGS, settings)
                dispatch('saveSettings')
            },
            loadContests({commit, getters}) {
                api.contest.getAllContests().then((data) => {
                    commit(types.SET_CONTESTS, data)

                    if (data.length) {
                        commit(types.SET_CURRENT_CONTEST, getters.contests[0])
                    }
                }).catch((err) => {
                    console.error(err)
                    throw err
                })
            },
            createContest({commit, getters}, name) {
                api.contest.createNewContest(name).then((data) => {
                    commit(types.ADD_CONTEST, data)
                    commit(types.SET_CURRENT_CONTEST, getters.contests[0])
                }).catch()
            },
            deleteContest({getters, commit}, contestId) {
                api.contest.deleteContest(contestId).then(() => {
                    commit(types.DELETE_CONTEST, contestId)

                    if (getters.contests.length > 0) {
                        commit(types.SET_CURRENT_CONTEST, getters.contests[0])
                    }
                }).catch()
            },
            setCurrentContest({commit, getters}, contestId) {
                commit(types.SET_CURRENT_CONTEST, getters.contestById(contestId))
            },
            saveCurrentContest({getters}, form) {
                const toSave = getters.currentContest;
                toSave.name = form.contestName
                toSave.settings.bgColor = form.popupBackground
                toSave.settings.color = form.textColor
                toSave.settings.iconColor = form.shareIcon
                toSave.settings.iconPosition = form.iconPlacement
                toSave.settings.end_date = form.end

                if (form.end || form.end == 'true') {
                    toSave.settings.date =
                        typeof form.date == 'object'
                            ? new Date(form.date.getTime() - (form.date.getTimezoneOffset() * 60000))
                                .toISOString().split('T')[0] // account for timezone
                            : form.date.includes('T')
                                ? form.date.split('T')[0]
                                : form.date
                    toSave.settings.hours = form.time.getHours()
                    toSave.settings.minutes = form.time.getMinutes()
                }

                toSave.settings.share_description = form.shareDescription
                toSave.settings.points_description = form.collectedPointsText
                toSave.settings.rank_description = form.rankText
                toSave.settings.youtube_url = form.youTubeVideo
                toSave.settings.timezone = form.timeZone

                return api.contest.saveContest(toSave).then((data) => {
                    return data.result
                }).catch()
            },
            saveSettings({getters}) {
                api.user.saveSettings({settings: getters.settings}).then().catch((err) => {
                    console.error(err)
                    throw err
                })
            },
            connectYouZign({commit}, youZignSettings) {
                api.user.connectYouZign(youZignSettings).then((data) => {
                    commit(types.SET_YOUZIGN, data)
                }).catch()
            }
        },
    })
}
