<template>
  <div class="sidebar-wrapper" :class="{mobileShow, specialSidebar}">
    <div class="opacity" @click="mobileShow = false"/>
    <div class="sidebar">
      <el-menu router class="second">
        <component :is="sidebarC"/>
      </el-menu>
      <el-menu router :class="{collapsed: isCollapsed}" class="first">
        <div class="nav-container">
          <Modal v-model="dialogVisible"/>
          <h3 class="nav-section">Menu</h3>
          <el-menu-item @click="dialogVisible = true">
            <ii-rocket/>
            <span>Create</span>
          </el-menu-item>
          <router-link to="/campaigns" class-active="active">
            <!-- :class="{'active': true }" -->
            <el-menu-item>
              <ii-folder/>
              <span>Campaigns</span>
            </el-menu-item>
          </router-link>
          <router-link to="/settings" class-active="active">
            <el-menu-item>
              <ii-settings/>
              <span>Settings</span>
            </el-menu-item>
          </router-link>
          <router-link to="/contest" class-active="active">
            <el-menu-item>
              <ii-contest/>
              <span>Contests</span>
            </el-menu-item>
          </router-link>
          <h3 class="nav-section">Support</h3>
          <a href="http://training.iglooapp.com/" target="_blank">
            <el-menu-item>
              <ii-tutorial/>
              <span>Tutorials</span>
            </el-menu-item>
          </a>
          <a href="http://support.skola.com/" target="_blank">
            <el-menu-item>
              <ii-support/>
              <span>Support</span>
            </el-menu-item>
          </a>
        </div>
        <div class="nav-container-dark">
          <h3 class="nav-section">Upgrades</h3>
             <!-- <a href="https://iglooapp.com/private-invitation/allaccess-trial/" target="_blank"> -->
             <a href="http://reborn.iglooapp.com/allaccess-trial/" target="_blank">
            <el-menu-item>
              <ii-windows/>
              <span>All Access Pass</span>
            </el-menu-item>
          </a>
          <div class="admin" v-if="user && user.user.type == 'admin'">
            <h3 class="nav-section">Admin</h3>
            <router-link to="/adm-templates" class-active="active">
              <el-menu-item>
                <ii-folder/>
                <span>Admin Templates</span>
              </el-menu-item>
            </router-link>
            <router-link to="/adm-sections" class-active="active">
              <el-menu-item>
                <ii-link/>
                <span>Admin Sections</span>
              </el-menu-item>
            </router-link>
            <router-link to="/adm-users" class-active="active">
              <el-menu-item>
                <ii-people/>
                <span>Users</span>
              </el-menu-item>
            </router-link>
          </div>
        </div>
        <!--          <el-menu-item index="#">-->
        <!--            <ii-gift/>-->
        <!--            <span>Bonuses</span>-->
        <!--          </el-menu-item>-->
      </el-menu>
    </div>
  </div>
</template>

<script>
import Modal from "@/components/Modal";
import CampaignSpecialSidebar from "@/components/CampaignSpecialSidebar";
import {mapGetters} from "vuex";

export default {
  components: {
    Modal,
  },
  data: () => ({
    specialSidebar: false,
    dialogVisible: false,
    isCollapsed: false,
    mobileShow: false,
    sidebarC: CampaignSpecialSidebar,
  }),
  watch: {
    '$route': {
      immediate: true,
      deep: true,
      handler(val) {
        this.isCollapsed = !!val.meta.sidebar;
        this.specialSidebar = !!val.meta.specialSidebar;

        if (typeof (val.meta.sidebar) != "boolean") {
          this.sidebarC = val.meta.sidebar;
        } else {
          this.sidebarC = CampaignSpecialSidebar;
        }
      }
    }
  },
  methods: {
    openSidebar() {
      this.mobileShow = !this.mobileShow;
    }
  },
  computed: {
    ...mapGetters(['user',]),
  },
  created() {
    this.$bus.$on('open-sidebar', this.openSidebar);
  }
}
</script>

<style lang="scss">
//.router-link-exact-active  {
//  background: red !important;
//  color: white !important;
//}
.active {

  .el-menu-item {
    color: white !important;
  }
}

.campaign-select {
  .el-input {
    border-radius: 0px !important;

    &__inner {
      background-color: $clr-nav-bg;
      height: 70px;
      border: none !important;
      border-radius: 0px !important;
    }
  }
}
</style>

<style lang="scss" scoped>
.opacity {
  position: absolute;
  top: 53px;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: black;
  opacity: 0;
  pointer-events: none;
  z-index: 1;
  transition: opacity 0.4s;
}

.sidebar {
  position: relative;
  width: 265px;
  height: 100%;
  display: flex;
  transition: all 0.4s ease, height 0s ease;
  z-index: 1;

  &-wrapper {
    width: 265px;
    display: flex;
  }

  .el-menu {
    overflow-y: auto;
  }

  .el-button {
    width: 100%;
    margin-left: 0;

    &#import-button {
      color: #1b4000;
    }
  }
}

.campaign {
  height: 57px;
  background-color: $clr-nav-bg;
  padding: 0 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  cursor: pointer;
  color: $clr-txt-nav;
  transition: all ease 0.3s;

  &-name {

  }

  &-icons {

  }

  svg {

  }

  &:hover {
    background-color: $clr-primary-hover;
    color: white;
  }
}

.buttons {
  padding: 0 12px;
  margin-top: 25px;

  .el-button {
    margin-bottom: 15px;
  }
}

.el-select {
  width: 100%;

  .el-input {
    &__inner {
      height: 70px;
    }
  }
}

.first {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 265px;
  height: 100%;
  transition: all 1s ease;
  overflow: hidden;
}

.el-menu {
  width: 265px;
  display: flex;
  flex-direction: column;
  border: none;

  &::before, &::after {
    display: none;
  }

  a {
    text-decoration: none !important;
    text-underline: none !important;
  }

  &-item {
    height: 34px;
    margin-bottom: 6px;
    padding-left: 40px !important;
    padding-right: 0px;
    color: $clr-txt-nav;
    line-height: 34px;
    background-color: transparent !important;
    transition: all 1s ease;

    &:hover, &.is-active, .active {
      color: white;
    }

    span {
      transition: opacity 1s ease;
      opacity: 1;
    }
  }

  svg {
    margin-right: 10px;
    height: 20px;
  }

  .nav-container {
    background-color: $clr-nav-bg;
    padding-bottom: 52px;
    font-weight: 700;
    transition: all 1s ease;

    &-dark {
      transition: all 1s ease;
      background-color: $clr-nav-bg-dark;
      flex-grow: 1;

      .admin {
        color: #e8b254 !important;

        .el-menu-item {
          color: #e8b254 !important;
        }

        svg {
          width: 20px;
          color: #e8b254 !important;
        }
      }
    }
  }

  .nav-section {
    color: white;
    font-weight: 500;
    transition: all 1s ease;
    height: 17px;
    opacity: 1;
  }
}

.second {
  padding-left: 36px;
  background-color: $clr-nav-bg;

  .nav-container, .nav-container-dark {
    background: $clr-nav-bg;
  }
}

.collapsed {
  width: 36px;

  .nav-section {
    overflow: hidden;
    opacity: 0;
    height: 0;
    margin-top: 0;
    margin-bottom: 0;
    transform: translate(-30px, -15px);
  }

  .nav-container, .nav-container-dark {
    padding: 23px 8px;
    background-color: $clr-nav-bg-dark;
  }

  .el-menu-item {
    padding: 0 !important;

    svg {
      transition: all ease 0.2s;

      &:hover {
        transform: scale(1.06) rotate(10deg);
      }
    }

    span {
      opacity: 0;
    }
  }
}

@media (max-width: $media-tablet) {
  .specialSidebar {
    .sidebar {
      left: -265px;
      width: 0px;
    }

    &.sidebar-wrapper {
      width: 0px;
    }

    &.mobileShow {
      .sidebar {
        position: absolute;
        top: 53px;
        left: 0;
        width: 265px;
        bottom: 0;
        height: unset;
      }

      .opacity {
        opacity: 0.8;
        pointer-events: auto;
      }
    }
  }
}

@media (max-width: $media-mobile) {
  .sidebar {
    left: -265px;
    width: 0px;
  }

  .sidebar-wrapper {
    width: 0px;
  }

  .mobileShow {
    .sidebar {
      position: absolute;
      top: 53px;
      left: 0;
      width: 265px;
      bottom: 0;
      height: unset;
    }

    .opacity {
      opacity: 0.8;
      pointer-events: auto;
    }
  }
}

.nav-section {
  margin-top: 52px;
  margin-left: 40px;
  font-size: 14px;
  text-transform: uppercase;
}
</style>
