<template>
  <div class="sections">
    <div class="title">Section Templates Manager
      <el-button class="save" type="primary" :disabled="!saveSections" @click="saveSections">save</el-button>
    </div>
    <draggable class="content" :list="sections" handle=".drag" draggable=".row">
      <div class="row" v-for="(s, idx) in sections" :key="s.id">
        <div class="drag">
          <fa-drag/>
        </div>
        <div class="name">
          <el-input v-model="sections[idx].name"/>
        </div>
        <div class="controls">
          <el-button @click="sections[idx].tpl_active = !sections[idx].tpl_active" :class="{'active': sections[idx].tpl_active}">Active</el-button>
          <el-button @click="sections[idx].tpl_new = !sections[idx].tpl_new" :class="{'active': sections[idx].tpl_new}">New</el-button>
          <el-select v-model="sections[idx].tpl_type">
            <el-option value="free"/>
            <el-option value="downsell"/>
            <el-option value="monthly"/>
          </el-select>
        </div>
        <div class="date">
          Publish Date:
          <el-select v-model="sections[idx].month">
            <el-option value="01"/>
            <el-option value="02"/>
            <el-option value="03"/>
            <el-option value="04"/>
            <el-option value="05"/>
            <el-option value="06"/>
            <el-option value="07"/>
            <el-option value="08"/>
            <el-option value="09"/>
            <el-option value="10"/>
            <el-option value="11"/>
            <el-option value="12"/>
          </el-select>
          /
          <el-select v-model="sections[idx].year">
            <el-option value="2016"/>
            <el-option value="2017"/>
            <el-option value="2018"/>
            <el-option value="2019"/>
            <el-option value="2020"/>
            <el-option value="2021"/>
            <el-option value="2022"/>
            <el-option value="2023"/>
            <el-option value="2024"/>
            <el-option value="2025"/>
          </el-select>
        </div>
        <div class="preview">
          <el-button type="primary" @click="preview(idx)">
            <fa-preview/>
          </el-button>
        </div>
      </div>
    </draggable>
  </div>
</template>

<script>
export default {
  name: "Sections",
  data: () => ({
    loading: false,
    sections: null,
  }),
  created() {
    this.loading = true
    this.$api.admin.getSectionTemplates()
        .then(res => {
          this.sections = res
        })
        .finally(() => (
            this.loading = false
        ))
  },
  methods: {
    saveSections() {
      this.$api.admin.saveSections(this.sections)
    },
    preview(idx) {
      const current = this.sections[idx]
      window.open(`https://launchigloo.com/preview-section/${current.id}?c=${current.code}`, '_blank')
    },
  },
}
</script>

<style lang="scss">
.sections {

  .el-button {
    //border: 1px solid $clr-grey;

    &:hover {
      background-color: $clr-primary-light !important;
      color: white;
    }
  }

  .row{
    .controls {
      .el-select {
        width: 100px;
        margin: 0 10px;
      }
    }
  }

  .preview {
    .el-button {
      width: 30px !important;
      height: 30px !important;
      padding: 0;
      fill: white;
    }
  }

  .name {
    .el-input {
      //width: 220px;
    }
  }

  .date {
    .el-select {
      width: 80px;
    }
  }

  .el-button {

    &.active {

      background: $clr-primary;
      color: white;
    }
  }
}
</style>

<style lang="scss" scoped>
.sections {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  padding: 0 20px;

  .title {
    width: 100%;
    align-self: flex-start;
    display: flex;
    justify-content: space-between;
    font-size: 24px;
    font-weight: 600;
  }

  .content {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 15px;

    .row {
      width: 100%;
      height: 60px;
      padding: 10px 10px;

      background-color: white;
      border-radius: 6px;

      box-shadow: 0px 0px 28px rgba(0, 0, 0, 0.05);

      display: flex;
      align-items: center;
      gap: 12px;

      .drag, .preview {
        width: 35px;
        height: 100%;
        display: flex;
        align-items: center;

        svg {
          width: 20px;
          height: 20px;
        }
      }

      .name {
        flex-grow: 1;
      }

      .controls {

      }
    }
  }
}
</style>