<template>
  <div v-if="contests" class="nav-container-dark contest-special">
    <ContestModal v-model="dialogVisible"/>
    <el-select
        v-model="value"
        placeholder="Select"
        popper-class="contest-select"
        class="contest-select"
        @change="setCurrentContest">
      <el-option
          v-for="c in contests"
          :key="c.id"
          :label="c.name"
          :value="c.id">
      </el-option>
    </el-select>
    <div v-for="c in contests" :key="c.id" class="contest"
         @click="setCurrentContest(c.id)"
         :class="{'active': c.id == currentContest.id }">
        <span class="contest-name" :title="c.name">
          {{ c.name }}
        </span>
      <span class="contest-icons">
          <el-button circle class="copy" @click.stop="handleDuplicateContest(c.id)">
              <ii-copy/>
          </el-button>
        <el-button circle class="delete" @click.stop="handleDeleteContest(c.id)">
              <ii-delete/>
        </el-button>
      </span>
    </div>
    <div class="buttons">
      <el-button type="info" id="import-button" @click="handleNewContest">
        Create New Contest
      </el-button>
    </div>
  </div>
</template>

<script>
import {mapActions, mapGetters} from "vuex";
import ContestModal from "@/components/ContestModal";

export default {
  name: "ContestSpecialSidebar",
  components: {
    ContestModal
  },
  data: () => ({
    value: null,
    dialogVisible: false,
  }),
  computed: {
    ...mapGetters(['currentContest', 'contests']),
  },
  watch: {
    currentContest: {
      handler(v) {
        if (v) {
          this.value = v.name;
        }
      },
      deep: true,
      immediate: true,
    },
  },
  methods: {
    ...mapActions(['setCurrentContest', 'createContest', 'deleteContest', 'duplicateContest']),
    handleNewContest() {
      this.dialogVisible = true;
    },
    handleDeleteContest(id) {
      this.deleteContest(id)
    },
    handleDuplicateContest(id) {
      this.duplicateContest(id)
    },
  }
}
</script>

<style lang="scss">
.contest-select {
  .el-input {
    border-radius: 0px !important;

    &__inner {
      background-color: $clr-nav-bg;
      height: 70px;
      border: none !important;
      border-radius: 0px !important;
    }
  }
}
</style>

<style lang="scss" scoped>

.sidebar {
  position: relative;
  width: 265px;
  height: 100%;
  display: flex;
  transition: all 0.4s ease, height 0s ease;
  z-index: 1;

  &-wrapper {
    width: 265px;
    display: flex;
  }

  .el-button {
    width: 100%;
    margin-left: 0;

    &#import-button {
      color: #1b4000;
    }
  }
}

.contest {
  height: 57px;
  background-color: $clr-nav-bg;
  padding: 0 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  cursor: pointer;
  color: $clr-txt-nav;
  transition: all ease 0.3s;

  &-name {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }

  &-icons {
    min-width: 70px;
    display: flex;
    justify-content: space-evenly;

    .el-button {
      width: 30px;
      height: 30px;
      background-color: transparent;
      color: $clr-txt-nav;
      padding: 0;

      &:hover {
        color: white;
        transform: rotate(12deg);
      }

      .svg {
        width: 100%;
        height: 100%;
        margin: 0;
      }

      .copy {

      }

      .delete {

      }
    }
  }

  svg {

  }

  &.active, &:hover {
    background-color: $clr-primary;
    color: white;
  }
}

.buttons {
  padding: 0 12px;
  margin-top: 25px;

  .el-button {
    margin-bottom: 15px;
    color: white !important;
  }
}

.el-select {
  width: 100%;

  .el-input {
    &__inner {
      height: 70px;
    }
  }
}

.el-menu {
  width: 265px;
  display: flex;
  flex-direction: column;
  border: none;

  &::before, &::after {
    display: none;
  }

  &-item {
    height: 34px;
    margin-bottom: 6px;
    padding-left: 40px !important;
    padding-right: 0px;
    color: $clr-txt-nav;
    line-height: 34px;
    background-color: transparent !important;
    transition: all 1s ease;

    &:hover {
      color: white;
    }

    &:active, &.is-active {
      color: $clr-primary;
    }

    span {
      transition: opacity 1s ease;
      opacity: 1;
    }
  }

  svg {
    margin-right: 10px;
    height: 20px;
  }

  .nav-container {
    background-color: $clr-nav-bg;
    padding-bottom: 52px;
    font-weight: 700;
    transition: all 1s ease;

    &-dark {
      transition: all 1s ease;
      background-color: $clr-nav-bg;
      flex-grow: 1;
    }
  }

  .nav-section {
    color: white;
    font-weight: 500;
    transition: all 1s ease;
    height: 17px;
    opacity: 1;
  }
}

.collapsed {
  width: 36px;

  .nav-section {
    overflow: hidden;
    opacity: 0;
    height: 0;
    margin-top: 0;
    margin-bottom: 0;
    transform: translate(-30px, -15px);
  }

  .nav-container, .nav-container-dark {
    padding: 23px 8px;
    background-color: $clr-nav-bg-dark;
  }

  .el-menu-item {
    padding: 0 !important;
    svg {
      transition: all ease 0.2s;
      &:hover {
        transform: scale(1.06) rotate(10deg);
      }
    }
    span {
      opacity: 0;
    }
  }
}
</style>
