<template>
  <div class="settings">
    <!--  TODO inputs full-width  -->
    <div class="column">
      <div class="card" id="wordPress">
        <div class="row title">WordPress Settings</div>
        <div class="row split">Download the plugin and use the API key to activate it</div>
        <div class="row content">
          <div v-if="settings && settings.api_key">
            <strong>API key:</strong> {{ settings.api_key }}
          </div>
          <div>
            <el-button type="primary" @click="wpPlugin">
              <ii-download/>
              iGloo Plugin
            </el-button>
            <el-button type="primary" @click="memberPlugin">
              <ii-download/>
              Membership Plugin
            </el-button>
          </div>
        </div>
      </div>
      <div class="card" id="domains">
        <div class="row title">
          <div class="header-text">
            My WordPress Domains
          </div>
          <div class="counter">
            <div class="counter-number" v-if="user && user.user.settings.num_domains">{{ domains.length }} of
              {{ user.user.settings.num_domains }}
            </div>
            <div class="counter-text">
              domains currently used
            </div>
          </div>
        </div>
        <div class="row content" v-if="domains.length > 0">
          <div class="list-elements" v-for="d in domains" :key="d.id">
            <div>
              <span>{{ d.domain }}</span>
            </div>
            <el-button circle class="option-btn" id="domain-delete">
              <ii-delete/>
            </el-button>
          </div>
        </div>
      </div>
      <div class="card" id="youZign">
        <div class="row title">YouZign Settings</div>
        <div class="row content first">
          <div class="resp-input">
            <div class="resp-text">
              Token
            </div>
            <div class="resp-control">
              <el-input v-model="youZign.token"/>
            </div>
          </div>
          <div class="resp-input">
            <div class="resp-text">
              API key
            </div>
            <div class="resp-control">
              <el-input v-model="youZign.apikey"/>
            </div>
          </div>
        </div>
        <div class="row content second">
          <div>
            <el-button
                type="primary"
                :disabled="youZign.token.length == 0 || youZign.apikey.length == 0"
                @click="youZignConnect">
              Save Settings
            </el-button>
          </div>
        </div>
        <div class="success-text" v-if="yzsuccess">
          Connected successfully!
        </div>
      </div>
      <div class="card" id="storage">
        <div class="row title">S3 storage</div>
        <div class="row split">Add an S3 bucket to store your images</div>
        <div class="row content fullheight">
          <div class="resp-input">
            <div class="resp-text">
              Endpoint
            </div>
            <div class="resp-control">
              <el-input v-model="storageSettings.endpoint"/>
            </div>
          </div>
          <div class="resp-input">
            <div class="resp-text">
              Region
            </div>
            <div class="resp-control">
              <el-input v-model="storageSettings.region"/>
            </div>
          </div>
          <div class="resp-input">
            <div class="resp-text">
              Access ID
            </div>
            <div class="resp-control">
              <el-input v-model="storageSettings.id"/>
            </div>
          </div>
          <div class="resp-input">
            <div class="resp-text">
              Access Token
            </div>
            <div class="resp-control">
              <el-input v-model="storageSettings.secret"/>
            </div>
          </div>
          <div class="resp-input">
            <div class="resp-text">
              Bucket Name
            </div>
            <div class="resp-control">
              <el-input v-model="storageSettings.bucketName"/>
            </div>
          </div>
          <el-button type="primary" @click="connectStorage">Connect</el-button>
        </div>
      </div>
    </div>
    <div class="column column-second">
      <div class="card" id="username">
        <div class="row title">Your Username</div>
        <div class="row split" id="custom-split">
          Please specify your desired username (itll be used in your page urls)
        </div>
        <div class="row content">
          <div class="resp-input">
            <div class="resp-text">
              Username
            </div>
            <div class="resp-control">
              <el-input v-model="username"/>
            </div>
          </div>
          <div>
            <el-button type="primary" :disabled="username.length == 0" @click="handleSaveUsername">Save Username
            </el-button>
          </div>
        </div>
        <div class="success-text" v-if="usernamesuccess">
          Username Saved!
        </div>
        <div class="success-text" v-if="usernameError">
          Username already in use, please choose another one.
        </div>
      </div>
      <div class="card" id="autoresponder">
        <div class="row title">Autoresponder Settings</div>
        <div class="row content">
          <div class="resp-input">
            <div class="resp-text">
              Autoresponder
            </div>
            <div>
              <el-select v-model="autoresponder" class="resp-control">
                <el-option v-for="(val, key) in autoresponderOptions" :key="key" :value="key" :label="val.label"/>
              </el-select>
            </div>
          </div>
        </div>
        <div v-if="autoresponder && autoresponderOptions[autoresponder]" class="row content fullheight">
          <component :is="autoresponderOptions[autoresponder].component"
                     :form="autoresponderSettings"
                     class="selectedAutoresponder"/>
          <el-button type="primary" v-if="!(autoresponder == 'aweber')" @click="handleAutoresponderAdd">Add Account
          </el-button>
          <div class="success-text" v-if="autoresponderError">
            Invalid settings provided for autoresponder! Please try again.
          </div>
        </div>
        <div class="row content fullheight" v-if="query && query.get('aweber')">
          Your aweber account has been successfully validated! Please specify the name for the account.
          <div class="resp-input">
            <div class="resp-text">
              Account name
            </div>
            <div>
              <el-input v-model="autoresponderSettings.name"/>
            </div>
          </div>
          <el-button type="primary" @click="handleAutoresponderAdd">
            Add Account
          </el-button>
        </div>
      </div>
      <div class="card" id="my-autoresponders" v-if="settings && settings.autoresponders">
        <div class="row title">My Autoresponder Accounts</div>
        <div class="row content">
          <div class="list-elements" v-for="s in settings.autoresponders" :key="s.id">
            <div>
              <span>{{ s.name }}</span>
            </div>
            <el-button circle class="option-btn" id="domain-delete" @click="handleAutoresponderDelete(s.id)">
              <ii-delete/>
            </el-button>
          </div>
        </div>
      </div>
      <div class="card" v-if="settings && settings.storage">
        <div class="row title">My Connected S3 storage</div>
        <div class="row content">
          <div class="list-elements">
            <span> {{ settings.storage.endpoint }} </span>
            <el-button circle class="option-btn" id="domain-delete" @click="awsDelete">
              <ii-delete/>
            </el-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import {mapActions, mapGetters} from "vuex";
import Activecampaign from "@/components/autoresponders/Activecampaign";
import Aweber from "@/components/autoresponders/Aweber";
import Zoomus from "@/components/autoresponders/Zoomus";
import Clevermessenger from "@/components/autoresponders/Clevermessenger";
import Everwebinar from "@/components/autoresponders/Everwebinar";
import Webinarjam from "@/components/autoresponders/Webinarjam";
import Sendlane from "@/components/autoresponders/Sendlane";
import Mailchimp from "@/components/autoresponders/Mailchimp";
import Getresponse from "@/components/autoresponders/Getresponse";
// import Markethero from "@/components/autoresponders/Markethero";

export default {
  name: "Settings",
  components: {},
  data: () => ({
    yzsuccess: false,
    usernamesuccess: null,
    usernameError: null,
    username: '',
    youZign: {
      token: '',
      apikey: '',
    },
    autoresponder: '',
    autoresponderOptions: {
      aweber: {label: "Aweber", component: Aweber},
      getresponse: {label: "Getresponse", component: Getresponse},
      mailchimp: {label: "Mailchimp", component: Mailchimp},
      ac: {label: "Active Campaign", component: Activecampaign},
      sendlane: {label: "Sendlane", component: Sendlane},
      // markethero: {label: "Market Hero", component: Markethero},
      webinarjam: {label: "Webinar Jam", component: Webinarjam},
      everwebinar: {label: "Ever Webinar", component: Everwebinar},
      clevermessenger: {label: "Clever Messenger", component: Clevermessenger},
      zoomus: {label: "ZoomUs", component: Zoomus},
    },
    autoresponderSettings: {
      api_key: '',
      name: '',
      url: '',
      key: '',
      account_id: '',
      hash_key: '',
      api_secret: '',
      token: '',
      secret: '',
    },
    storageSettings: {
      endpoint: '',
      region: '',
      id: '',
      secret: '',
      bucketName: '',
    },
    storageConnecting: false,
    storageErr: false,
    autoresponderError: null,
    query: new URLSearchParams()
  }),
  created() {
    const split = window.location.href.split('?')
    if (split.length > 1) {
      this.query = new URLSearchParams(split[1]);
    }
  },
  computed: {
    ...mapGetters(['settings', 'user', 'domains']),
    autorespondersData() {
      return {
        platform: this.query.has('aweber') ? 'aweber' : this.autoresponder,
        ...this.autoresponderSettings,
      };
    },
  },
  watch: {
    user: {
      handler(user) {
        if (user) {
          this.username = user.user.username
        }
      },
      deep: true,
      immediate: true,
    },
    settings: {
      handler(settings) {
        if (settings) {
          if (settings.youzign) {
            this.youZign = settings.youzign
          }
          if(settings.storage) {
            this.storageSettings = {...settings.storage}
          }
        }
      },
      deep: true,
      immediate: true,
    },
  },
  methods: {
    ...mapActions([
      'saveUsername',
      'addAutoResponder',
      'deleteAutoResponder',
      'saveSettings',
      'connectYouZign',
      'addStorage',
      'deleteStorage'
    ]),
    handleSaveUsername() {
      this.saveUsername(this.username).then(() => {
        this.usernameError = false
        this.usernamesuccess = true
      }).catch(() => {
        this.usernameError = true
      })
    },
    handleAutoresponderAdd() {
      if (this.query.get('aweber')) {
        this.autoresponderSettings.token = this.query.get('aweberToken')
        this.autoresponderSettings.secret = this.query.get('aweberSecret')
      }

      this.addAutoResponder(this.autorespondersData).then((result) => {
        this.autoresponderError = false
        this.query = null
        if (result != 'success') {
          this.autoresponderError = true
        }
      })
    },
    handleAutoresponderDelete(respId) {
      this.deleteAutoResponder(respId)
    },
    handleSaveSettings() {
      this.saveSettings();
    },
    youZignConnect() {
      this.connectYouZign(this.youZign)
      this.yzsuccess = true
    },
    wpPlugin() {
      window.location.href = 'https://launchigloo.com/igloo-wp.zip'
    },
    memberPlugin() {
      window.location.href = 'https://launchigloo.com/igloo-membership.zip?v=1.01'
    },
    awsDelete() {
      this.deleteStorage()
    },
    async connectStorage() {
      let connectSuccess = true
      this.storageConnecting = true
      const vm = this
      const s3 = new window.AWS.S3({
        endpoint: this.storageSettings.endpoint.includes('amazonaws.com') ? undefined : this.storageSettings.endpoint,//'https://s3.ca-central-1.wasabisys.com',
        accessKeyId: this.storageSettings.id,
        secretAccessKey: this.storageSettings.secret,
        region: this.storageSettings.region,
        sslEnabled: true,
        hostPrefixEnabled: false,
        s3ForcePathStyle: true,
      })

      await new Promise((res, rej) => {
        s3.putObject({
          ACL: "public-read",
          Body: '{ "message": "igloo test S3 storage" }',
          Bucket: this.storageSettings.bucketName,
          Key: "igloo_storage_test.json",
          ContentType: 'application/json',
        }, function (err) {
          if (err) {
            return rej(err)
          }

          s3.deleteObject({
            Bucket: vm.storageSettings.bucketName,
            Key: "igloo_storage_test.json",
          }, (err) => {
            if (err) {
              return rej(err)
            }

            return res()
          })
        });
      }).catch(err => {
        console.error(err, err.stack)
        this.storageErr = true
        connectSuccess = false
      })

      if (connectSuccess) {
        this.addStorage(this.storageSettings)
        console.log('success')
      }
      this.storageConnecting = false
    },
  }
}
</script>

<style lang="scss">
.settings {
  padding-bottom: 40px;

  .resp-text, .card-text {
    min-width: 100px;
    text-align: right;
    padding-right: 10px;
  }

  .resp-input, .card-input {
    width: 100%;
    display: flex;
    align-items: center;

    .resp-control {
      width: 100% !important;
    }

    .el-input, .el-select {
      width: 100% !important;

      &__inner {
        width: 100% !important;
      }
    }

  }

  .el-button {
    background-color: $clr-btn-primary;

    &:hover {
      background-color: $clr-btn-hover;
    }
  }

  #domain-delete, .domain-delete {
    background-color: transparent;
    width: 35px;
    height: 35px;
    padding: 0;
    transition: all 0.3s ease;

    &:hover {
      background-color: $clr-red;
      color: white !important;
    }
  }

  .el-input, el-select {

    &__inner {
      border-radius: 4px !important;
      outline: none !important;
      border: 1px solid #D0D0D0 !important;
      height: 26px !important;
    }
  }

  .el-icon-arrow {
    &-up, &-down {
      line-height: 26px !important;
    }
  }
}
</style>

<style lang="scss" scoped>
.settings {

  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: flex-start;
  gap: 50px;

  @media (max-width: $media-desktop) {
    gap: 25px;
  }

  .column {
    display: flex;
    flex-direction: column;
    gap: 30px;

    @media (max-width: $media-desktop) {
      gap: 25px;
    }

    @media (max-width: $media-mobile) {
      gap: 15px;
    }
  }

  a {
    text-decoration: none;
    color: black;

    &:hover {
      text-decoration: underline;
      color: $clr-primary-hover;
    }
  }

  @media (max-width: $media-desktop) and (min-width: $media-tablet) {
    .column-second > .card {
      width: 320px;
    }
  }

  .card {

    .success-text {
      background-color: white;
      text-align: center;
      padding-bottom: 12px;
    }

    &-text {
      margin-right: 16px;
    }

    &-input {
      width: 100%;
      display: flex;
      align-items: center;
    }

    width: 500px;
    background-color: $clr-grey;

    box-shadow: 0px 0px 28px rgba(0, 0, 0, 0.05);

    @media (max-width: $media-desktop) {
      width: 360px;
    }

    @media (max-width: $media-tablet) {
      gap: 15px;
    }

    .el-button {
      height: 30px;
      padding: 0 30px;
      font-size: 10px;
    }

    .row {
      display: flex;
      align-content: center;
      justify-content: space-between;
      align-items: center;
      background-color: white;
      height: 84px;

      font-size: 14px;

      color: #101010;

      padding-top: 0;
      padding-bottom: 0;
      padding-left: 40px;
      padding-right: 40px;

      @media (max-width: $media-desktop) {
        padding-right: 50px;
      }

      &.content {
        .list-elements {
          display: flex;
          justify-content: space-between;
          align-items: center;
          width: 100%;
        }
      }

      &.fullheight {
        height: 100% !important;
        background-color: white;
        padding-top: 20px;
        padding-bottom: 20px;

        display: flex;
        flex-direction: column;
        gap: 20px;
      }

      &.split {
        height: 24px;
        background-color: $clr-bg-dark;
        margin-bottom: 0;
        font-size: 11px;
        font-weight: 700;
        color: $clr-grey-light;
        padding-left: 40px;
        padding-right: 40px;

        &#custom-split {
          height: 32px;
        }

        @media (max-width: $media-tablet) {
          padding-left: 35px;
        }

        @media (max-width: $media-mobile) {
          padding-left: 25px;

          font-size: 10px;
        }
      }

      &-text {
        font-size: 16px;
        font-weight: 600;
      }

      &.title {
        height: 74px;
        font-size: 20px;
        font-weight: 600;
        border-bottom: 1px solid $clr-bg-dark;
      }
    }
  }

  #wordPress {
    .el-button {
      padding: 0 8px;

      svg {
        margin-right: 10px;
      }
    }
  }

  #autoresponder {
    .row.content.fullheight {
      background-color: white;
    }

    .selectedAutoresponder {
      display: flex;
      flex-direction: column;
      width: 100%;
      gap: 30px;
    }
  }


  #wordPress, #username {

    & > .content {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 135px;
      flex-grow: 1;
      padding: 25px 40px;

      @media (max-width: $media-desktop) {
        font-size: 12px;
      }
    }
  }

  #wordPress {

    & > .content {
      align-items: flex-start;
    }
  }

  #domains, #my-autoresponders {
    height: 100%;

    .row.title {
      display: flex;
      justify-content: space-between;
      padding-right: 0;
      border-bottom: 1px solid $clr-bg-dark;
    }

    .row.content {
      height: 100%;
      display: flex;
      flex-direction: column;
      gap: 8px;
      padding-top: 8px;
      padding-bottom: 8px;
    }

    .header-text {

    }

    .counter {
      background-color: $clr-bg-dark;
      display: flex;
      padding-left: 25px;
      padding-right: 25px;
      height: 100%;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      &-number {
        font-size: 30px;
        font-weight: 700;
        margin-bottom: 7px;
      }

      &-text {
        font-size: 11px;
        font-weight: 700;
        width: 120px;
        text-align: center;

        @media (max-width: $media-mobile) {
          width: 100%;
        }
      }
    }
  }

  #autoresponder {

    .row.content {
      background-color: $clr-bg-dark;
      height: 62px;
    }

    .card-text {
      margin-right: 21px;
    }

    select {
      width: 200px;

      @media (max-width: $media-desktop) {
        width: 140px;
      }
    }
  }

  #youZign {


    .row.content {
      height: 71px;
      align-items: flex-start;
      padding-top: 25px;

      .card-text {
        width: 60px;
        text-align: right;
      }

      &.first {
        display: flex;
        flex-direction: column;
        height: 113px;
      }

      &.second {
        box-sizing: content-box;
        justify-content: center;

        height: 64px;
      }
    }
  }

  #vidello {

    .row.content {
      align-items: flex-start;
      padding-top: 25px;

      &.first {
        height: 71px;
      }

      &.second {
        box-sizing: content-box;
        justify-content: center;

        height: 64px;
      }
    }

    .el-button {

    }
  }
}
</style>
