        <el-table-column class-name="contacts-cell" min-width="200" property="name" label="Name">
          <template slot="header">
            Name
            <div id="delete" slot="header">
              <el-button id="export">Export</el-button>
              <el-button id="delete-btn">Delete</el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>

import Subscribers from "@/components/Subscribers";
import {mapGetters} from "vuex";

export default {
  components: { Subscribers },
  data() {
    return {
      tableData: [{
        email: 'example1@example.com',
        name: 'Tom',
      }, {
        email: 'example2example2example2@example.com',
        name: 'Tom',
      }, {
        email: 'example3@example.com',
        name: 'Tom',
      }, {
        email: 'example4@example.com',
        name: 'Tom',
      }]
    }
  },
  computed: {
    ...mapGetters(['currentCampaign', "emails"])
  }
}
</script>


<style lang="scss" scoped>
.funnel {
