  justify-content: center;
  align-items: flex-start;
  gap: 35px;
  padding: 0 30px;
  width: 100%;

  @media (max-width: $media-mobile) {
    flex-wrap: wrap;
  }

  .subscribe {
    position: absolute;
    top: -45px;
    right: 10px;
    width: 190px;
    height: 30px;

    display: flex;
    justify-content: space-between;

    svg {
      margin-right: 10px;
    }
  }

  #delete {
    display: inline-flex;
    justify-content: space-between;
    width: 105px;
    margin-left: auto;
  }

  .card {
    margin-top: 45px;
    position: relative;
    background-color: white;
    box-shadow: 0px 0px 28px rgba(0, 0, 0, 0.05);
    padding: 25px;
    width: 100%;

    &#templates {
      padding: 0;
    }

    @media (max-width: $media-mobile) {
      &#templates {
        margin-top: 0;
      }
    }

    .row {
      &.content {
        height: 140px;
        display: flex;
        flex-direction: column;

        &.half {
          height: 75px;
        }

        &.third {
          height: 50px;
        }

        .contest-name {
          color: $clr-funnel;
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 25px 30px;
        }

        .el-button {
          margin: 0 30px;
          border: 1px solid $clr-funnel;
          color: #3878CC;
        }

        .el-button + .el-button {
          margin-top: 10px;
          margin-bottom: 22px;
        }

