          transition: all ease 0.3s;
          height: 46px;
          color: $clr-funnel;
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 0 30px;

          &:hover {
            background-color: #F7F7F7;
          }
        }

        &.stretching {
          height: 100%;
        }
      }
    }

    &#templates {
      max-width: 315px;
      min-width: 230px;
      flex-shrink: 1;
      flex-grow: 2;

      .row.content {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
      }
    }

