  color: $clr-funnel;

  @media (max-width: $media-mobile) {
    font-size: 10px;
  }

  .cell {
    display: flex !important;
  }
}

.subscribe {
  .el-button {

    height: 30px;
    padding: 0 10px;

    font-size: 10px !important;
    font-weight: 500 !important;
    color: $clr-primary;
    border: 1px solid $clr-funnel !important;
    background-color: transparent;
  }
}


#delete {
