<template>
  <el-dialog
      class="change-password-modal"
      :before-close="handleClose"
      :visible.sync="value"
      :append-to-body="true">
    <el-container>
      <span class="title">Change Password</span>
    </el-container>
    <form autocomplete="off">
      <el-input v-model="password" type="password" placeholder="Enter new password:" style="margin-bottom: 20px"/>
      <el-input v-model="confirm" type="password" placeholder="Confirm your password:"/>
    </form>
    <span slot="footer" class="dialog-footer">
    <el-button
        type="primary"
        @click="handleReset"
        :disabled=
            "
        password.length == 0
        || confirm.length == 0
        || !(password.length === confirm.length)
        "
        id="modal-create-campaign">Change</el-button>
    <el-button @click="handleClose">Close</el-button>
  </span>
    <div class="err" v-if="resetError">
      There was an error changing password. <br>
      please try again later.
    </div>
  </el-dialog>
</template>

<script>
import {mapActions} from "vuex";

export default {
  props: {
    value: {type: Boolean, required: true,}
  },
  methods: {
    ...mapActions(['changePassword']),
    handleClose() {
      this.$emit('input', false);
    },
    handleReset() {
      this.changePassword(this.password).then((res) => {
        if(res.success) {
          this.resetError = false
          this.handleClose()
        } else {
          this.resetError = true
        }
      })
    },
  },
  data: () => ({
    password: '',
    confirm: '',
    resetError: false
  })
};
</script>

<style lang="scss">
.change-password-modal {
  width: 100% !important;
  display: flex;
  align-items: center;
  justify-content: center;

  .err {
    margin-top: 20px;
    text-align: center;
    font-size: 16px;
  }

  .el-dialog {
    background-color: $clr-bg-light;
    border-radius: 15px;
    margin: auto 30px;
    width: 100%;
    margin-top: auto !important;

    &__body {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    .el-input {
      display: flex;
      justify-content: center;

      &__inner {
        height: 45px;
        border: none;
        outline: none;
        border-radius: 15px;
        text-align: center;
      }
    }

    &__wrapper {
      width: 100% !important;
    }

    @media (max-width: $media-tablet) {
      max-width: 490px;

      input {

        width: 260px;
      }
    }

    @media (min-width: $media-tablet) {
      width: 650px;

      input {

        width: 350px;
      }
    }

    @media (max-width: 470px) {
      max-width: 340px;

      input {

        width: 260px;
      }
    }

    &__title {
      display: flex;
    }

    &__body {
      padding-bottom: 25px;

      .title {
        font-size: 24px;
        margin: auto auto 35px;
      }
    }

    &__footer {
      display: flex;
      justify-content: center;
    }
  }
}
</style>

<style lang="scss" scoped>

</style>
