<template>
  <div class="campaign">
    <DeleteModal
        v-model="deletePromptVisible"
        :step-id="deleteId"
        :step-name="deleteName"
        @closed="deletePromptVisible = false"/>
    <template v-if="selectedComponent == null">
      <el-row v-if="currentPage && currentPage.page" type="flex" :gutter="50" style="flex-wrap: wrap;">
        <el-col :span="24" style="margin-bottom: 20px; margin-right: 0; padding-right: 40px" class="team">
          <!-- <Team/> -->
          <Subscribers
              v-if="currentCampaign"
              @toggle-settings="handleComponentChange('settings')"
              @toggle-subscribers="handleComponentChange('subscribers')"/>
        </el-col>
        <el-col :xs="24" :sm="24" :md="24" :lg="12" :xl="12">
          <el-container class="media-container" v-loading="thumbLoading">
            <div class="refresh" @click="updateThumb">
              <ii-refresh/>
            </div>
            <img :src="thumb" @error="changeThumb">
          </el-container>
        </el-col>
        <el-col :xs="24" :sm="24" :md="24" :lg="12" :xl="12" class="editor-controls">
          <div class="campaign-name-input">
            <el-input v-model="form.pageName" v-debounce:1s="handleSave" :disabled="loading" placeholder="Aqua Landing Page"/>
            <div class="editor-settings">
              <el-button id="edit" @click="editPage">
                <ii-edit/>
              </el-button>
              <!-- TODO popups too wide -->
              <div class="icons">
                <el-popover placement="top" width="200" trigger="hover"
                            popper-class="button-popup">
                  <el-button circle slot="reference" @click="previewPage">
                    <ii-message/>
                  </el-button>
                  Preview
                </el-popover>
                <el-popover placement="top" width="200" trigger="hover"
                            popper-class="button-popup">
                  <el-button circle slot="reference" @click="duplicate">
                    <ii-copy/>
                  </el-button>
                  Duplicate
                </el-popover>
                <el-popover placement="top" width="200" trigger="hover"
                            popper-class="button-popup">
                  <el-button circle slot="reference" @click="zip">
                    <ii-cloud/>
                  </el-button>
                  Export Page as ZIP file. It will contain html, images, css and javascript files that you can upload to
                  your webserver
                </el-popover>
                <el-popover placement="top"
                            width="200"
                            trigger="hover"
                            popper-class="button-popup"
                            v-if="user && user.user.settings.page_templates">
                  <el-button circle slot="reference" @click="json">
                    <ii-graphs/>
                  </el-button>
                  Export Page Template. You can then import it by clicking on the button on bottom left
                </el-popover>
                <el-popover placement="top" width="200" trigger="hover"
                            popper-class="button-popup">
                  <el-button circle slot="reference" @click="handleDeleteStep">
                    <ii-cross/>
                  </el-button>
                  Delete page
                </el-popover>
              </div>
            </div>
          </div>
          <div class="url">
            <div class="def-pg-url">Default page url</div>
            <div class="url-builder">
              <div class="url-prefix">
                {{
                  `${currentCampaign.settings.domain}/${currentCampaign.name}`
                  ||'sample-url.igloopage.com/campaign-name'
                }}/
              </div>
              <div class="url-input">
                <el-input v-model="form.pageUrl" v-debounce:1s="handleSave" :disabled="loading" placeholder="Page url"></el-input>
              </div>
            </div>
          </div>
          <div class="code">
            <el-input type="textarea" v-debounce:1s="handleSave" :rows="6" class="campaign-input-area" placeholder="Header Code"
                      v-model="form.header" :disabled="loading" />
<!--            @blur="handleSave"      -->
            <el-input type="textarea" v-debounce:1s="handleSave" :rows="6" class="campaign-input-area" placeholder="Footer Code"
                      v-model="form.footer" :disabled="loading"/>
          </div>
        </el-col>
      </el-row>
      <component :is="template" v-else-if="showSelectTemplate"/>
      <div class="card no-pages" v-else>
        <div class="row">
          No pages here yet! Create one in the sidebar on the left.
        </div>
      </div>
    </template>
    <template v-else>
      <component
          v-if="selectedComponent !== null"
          @toggle-subscribers="handleComponentChange('subscribers')"
          @toggle-settings="handleComponentChange('settings')"
          :is="selectedComponent"/>
    </template>
  </div>
</template>

<script>
import {mapActions, mapGetters} from "vuex";
import Team from "@/components/Team";
import SelectTemplate from "@/components/SelectTemplate";
import Subscribers from "@/components/Subscribers";
import Funnel from "@/components/Funnel";
import PageSettings from "@/components/PageSettings";
import DeleteModal from "@/components/DeleteModal";

export default {
  name: "Campaign",
  components: {Team, SelectTemplate, Subscribers, DeleteModal},
  data: () => ({
    form: {
      pageName: '',
      header: '',
      footer: '',
      pageUrl: '',
    },
    thumb: '',
    selectedComponent: null,
    loading: false,
    template: SelectTemplate,
    thumbLoading: false,
    deletePromptVisible: false,
    deleteId: '',
    deleteName: '',
  }),
  methods: {
    ...mapActions([
      'setCurrentCampaign',
      "setStepProperty",
      "saveCampaign",
      'exportPage',
      'exportPageJson',
      'duplicateStep',
      'takeScreenshot'
    ]),
    changeThumb(e) {
      if (e.target) {
        if (!e.target.naturalWidth || !e.target.naturalHeight) {
          this.thumb = 'https://launchigloo.com/images/blank_page.png'
        }
      }
    },
    handleComponentChange(component) {
      if (component === 'subscribers') {
        if ((this.selectedComponent === Funnel)) {
          this.selectedComponent = null
        } else {
          this.selectedComponent = Funnel
        }
      } else {
        if ((this.selectedComponent === PageSettings)) {
          this.selectedComponent = null
        } else {
          this.selectedComponent = PageSettings
        }
      }
    },
    handleSave() {
      this.setStepProperty({property: 'name', value: this.form.pageName})
      this.setStepProperty({property: 'header', value: this.form.header})
      this.setStepProperty({property: 'footer', value: this.form.footer})
      this.setStepProperty({property: 'igloo_url', value: this.form.pageUrl})
      // this.loading = true;
      this.saveCampaign().finally(() => {
        // this.loading = false
      })
    },
    handleDeleteStep() {
      this.deleteId = this.currentCampaignPage
      this.deleteName = this.currentPage.name
      this.deletePromptVisible = true
    },
    editPage() {
      if (this.currentPage.page.new_builder == 1) {
        window.location.href = `https://app.launchigloo.com/?page=${this.currentPage.page.id}&token=${localStorage.getItem('token')}&exitUrl=${window.location.href}`
      } else {
        window.location.href = `https://launchigloo.com/autologin.html?token=${localStorage.getItem('token')}&redirect=${encodeURIComponent(`https://launchigloo.com/#/page/${this.currentPage.page.id}`)}`
      }
    },
    previewPage() {
      let code = this.currentPage.page.code
      window.location.href =
          `https://launchigloo.com/preview/${this.currentPage.page.id}`
          + (code ?
              '?c=' + code :
              '')
    },
    duplicate() {
      this.duplicateStep()
    },
    updateThumb() {
      this.thumbLoading = true
      this.takeScreenshot().finally(() => {
        this.thumbLoading = false
      })
    },
    zip() {
      this.exportPage().then((res) => {
        if (res.success) {
          window.location.href = `https://launchigloo.com/api/page/export-download?id=${this.currentPage.page.id}` +
              (this.currentPage.page.code ?
                  '&code=' + this.currentPage.page.code :
                  '')
        }
      })
    },
    json() {
      this.exportPageJson().then((res) => {
        if (res.success) {
          window.location.href = `https://launchigloo.com/api/page/export-json-download?id=${this.currentPage.page.id}` +
              (this.currentPage.page.code ?
                  '&code=' + this.currentPage.page.code :
                  '')
        }
      })
    },
  },
  computed: {
    ...mapGetters(['currentPage', "currentCampaign", 'user', 'currentCampaignPage']),
    showSelectTemplate() {
      return this.currentCampaign
          && this.currentCampaign.settings
          && this.currentCampaign.settings.steps
          && this.currentCampaign.settings.steps.length > 0;
    }
  },
  watch: {
    currentPage: {
      handler(v) {
        if (v) {
          this.form.pageName = v.name
          this.form.header = v.page.header
          this.form.footer = v.page.footer
          this.form.pageUrl = v.page.igloo_url || ''
          this.thumb = v.page.thumb || 'https://launchigloo.com/images/blank_page.png'
        }
      },
      immediate: true,
      deep: true,
    },
  },
  created() {
    const currentId = this.$route.params.id;
    this.setCurrentCampaign(currentId);
  },
  beforeRouteUpdate(to, from, next) {
    this.setCurrentCampaign(to.params.id);
    next();
  },
}
</script>

<style lang="scss">
.campaign {
  .el-textarea__inner {
    border-radius: 10px !important;
    border-color: transparent !important;
  }

  #edit {
    transition: all ease 0.2s;

    &:hover {
      transform: scale(1.04);
    }
  }

  .icons {
    .el-button {
      display: flex;
      justify-content: center;
      align-items: center;

      transition: all ease 0.2s;

      svg {
        transition: all ease 0.2s;
      }

      &:hover {
        transform: scale(1.09);

        svg {
          transform: scale(1.1) rotate(10deg);
        }
      }
    }
  }

  .campaign-name-input {

    .el-input__inner {
      width: 100% !important;
      height: 43px !important;
      border-radius: 10px !important;
      border: none !important;
      padding: 0 15px !important;
      margin-bottom: 25px !important;
    }
  }


  #campaign-name {
    height: 80px;
  }

  .url {
    display: flex;
    flex-direction: column;
    gap: 16px;

    .def-pg-url {
      font-weight: 500;
      font-size: 16px;
      text-transform: uppercase;
      color: #637076;
    }

    &-builder {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      gap: 5px;
    }

    &-prefix {
      color: #C5C5C5;
    }

    &-input {

      flex-grow: 1;

      .el-input {
        height: 43px;

        &__inner {

          width: 100% !important;
          height: 43px !important;
          border-radius: 10px !important;
          border: none !important;
          padding: 0 15px !important;
        }
      }
    }
  }
}

.button-popup {
  background-color: $clr-black !important;
  color: white !important;
  border: none !important;

  .popper__arrow:after {
    border-top-color: $clr-black !important;
  }
}
</style>

<style lang="scss" scoped>

.icons {
  display: flex;
  gap: 7px;

  svg {
    width: 13px;
    height: 13px;
  }
}

.card {
  display: flex;
  flex-direction: column;
  background-color: white;
  font-size: 16px;
  gap: 25px;
  margin: 15px;
  padding: 30px;
  width: 100%;
  box-shadow: 0px 0px 28px rgb(0 0 0 / 5%);
}

.team {
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
}

.campaign {
  margin: 0 -25px;
  display: flex;
  justify-content: center;
  padding: 25px;
  font-size: 14px;
  font-weight: 400;
  max-width: 1200px;

  .el-row {
    padding-top: 0;
  }
}

.editor-controls {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 20px;
}

.media-container {
  width: 100%;
  height: fit-content;
  max-width: 720px;
  margin-bottom: 20px;
  position: relative;

  .refresh {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 10px;
    right: 10px;
    width: 27px;
    height: 27px;
    border-radius: 50%;
    background-color: $clr-primary;
    color: white;
    padding: 6px;
    cursor: pointer;
    transition: all ease 0.3s;

    &:hover {

      transform: rotate(20deg);
    }

    svg {
      color: white;
    }
  }

  img {
    width: 100%;
    height: 100%;
  }
}

.editor-settings {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: -15px;
  margin-bottom: 30px;

  .el-button {
    background-color: $clr-btn-primary;
    color: white;
    height: 34px;
    width: 95px;
    padding: 0;

    &:hover {
      background-color: $clr-btn-hover;
    }

    &.is-circle {
      width: 34px;
    }
  }
}

.campaign-input {
  width: 100%;
  height: 43px;
  border-radius: 10px;
  border: none;
  padding: 0 15px;
  margin-bottom: 25px;

  &-area {
    padding: 0;
    margin-bottom: 25px;
  }

  &:focus-visible {
    outline: none;
  }
}

.code {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
</style>
