<template>
  <div class="login" v-if="!reset">
    <div class="login-form">
      <div class="logo">
        <ii-logo-old/>
      </div>
      <div class="err" v-if="loginFail">
        Username or password are incorrect.
      </div>
      <div class="info" v-else>
        LOGIN TO YOUR ACCOUNT BELOW
      </div>
      <div class="inputs">
        <el-input
            v-model="email"
            placeholder="Enter your username"
            name="email"
            class="input"
            @keyup.enter.native="handleLogin"/>
        <el-input
            v-model="password"
            placeholder="Enter your password"
            name="password"
            type="password"
            class="input"
            @keyup.enter.native="handleLogin"/>
      </div>
      <el-button :disabled="loading" v-loading="loading" type="info" id="login" @click="handleLogin">
        LOGIN TO IGLOO
      </el-button>
      <div class="forget" @click="reset = !reset">
        <ii-lock/>
        Forgot your password?
      </div>
    </div>
  </div>
  <div class="login" v-else>
    <div class="login-form">
      <div class="logo">
        <ii-logo-old/>
      </div>
      <div class="info" v-if="!resetSuccess">LOGIN TO YOUR ACCOUNT BELOW</div>
      <div class="inputs" v-if="!resetSuccess">
        <el-input
            v-model="email"
            placeholder="Enter your username"
            name="email"
            class="input"
            @keyup.enter.native="handleLogin"/>
      </div>
      <el-button :disabled="loading" v-loading="loading" type="info" id="instructions" @click="handleReset"
                 v-if="!resetSuccess">
        SEND INSTRUCTIONS
      </el-button>
      <div class="info" v-else>The instructions have been sent to the provided email</div>
      <div class="forget" @click="reset = !reset">
        <ii-lock/>
        Back to login
      </div>
    </div>
  </div>
</template>

<script>
import {mapActions} from "vuex";

export default {
  name: "Login",
  data: () => ({
    email: '',
    password: '',
    loading: false,
    reset: false,
    resetSuccess: false,
    loginFail: false
  }),
  methods: {
    ...mapActions(['login', 'resetPassword']),
    handleLogin() {
      this.loading = true;
      this.loginFail = false

      this.login({email: this.email, password: this.password}).catch(() => {
        this.loginFail = true
      }).finally(() => {
        this.loading = false;
      })
    },
    handleReset() {
      this.loading = true;
      this.resetPassword(this.email).then((res) => {
        if (res.success) {
          this.resetSuccess = true
        }
      }).finally(() => {
        this.loading = false;
      })
    }
  }
}
</script>

<style lang="scss">
.login {
  background-image: url("~@/assets/login_bg.jpg");

  .el-input__inner {
    width: 100%;
    height: 50px !important;
    background-color: white;
    text-align: center;
  }
}
</style>

<style lang="scss" scoped>
.login {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  .err {
    text-align: center;
    font-size: 16px;
    font-weight: 700;
    color: $clr-green;
  }

  &-form {
    background-color: $clr-primary;
    border-radius: 15px;
    //margin: auto 30px;
    width: 550px;
    height: 420px;
    padding: 30px 50px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    gap: 25px;

    .forget {
      color: #c5d6de;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;

      &:hover {
        color: white;
      }

      svg {
        width: 20px;
        height: 20px;
      }
    }

    .logo {

    }

    .info {
      font-size: 17px;
      color: white;
    }

    .inputs {
      width: 100%;
      display: flex;
      flex-direction: column;
      gap: 20px;
    }

    #login, #instructions {
      width: 100%;
      height: 50px;

      font-size: 20px;
      font-weight: 700;
    }
  }
}
</style>
