<template>
  <div class="users">
    <!--    <AdminConfirmModal-->
    <!--    v-model="confirmVisible"-->
    <!--    :confirm-text="confirmText"-->
    <!--    :confirm-action="confirmAction"/>-->
    <div class="title">Users Manager</div>
    <div class="cards">
      <div class="card-col">
        <div class="card">
          <div class="row title">
            Search for User
          </div>
          <div class="row">
            <el-input v-model="userSearch" placeholder="email"/>
          </div>
          <div class="row">
            <el-button type="primary" @click="search">Search</el-button>
          </div>
          <div class="row error" v-if="searchFail">
            user not found
          </div>
        </div>
        <div class="card">
          <div class="row title">
            Add User
          </div>
          <div class="row">
            <el-select v-model="createType" placeholder="Select User Type">
              <el-option label="Lite" value="lite"/>
              <el-option label="Standard" value="standard"/>
              <el-option label="Premium" value="premium"/>
              <el-option label="Affiliate" value="affiliate"/>
              <el-option label="Full options" value="full"/>
            </el-select>
          </div>
          <div class="row">
            <el-input v-model="createName" placeholder="username"/>
          </div>
          <div class="row">
            <el-input v-model="createEmail" placeholder="email"/>
          </div>
          <div class="row">
            <el-input v-model="createPass" placeholder="password"/>
          </div>
          <div class="row">
            <el-button
                type="primary"
                @click="addUser"
                :disabled="createEmail.length == 0 || createName.length == 0 || createPass.length < 10 || createType.length == 0">
              Add User
            </el-button>
          </div>
          <div
              class="row error"
              v-if="errMsg">
            {{ errMsg }}
          </div>
        </div>
      </div>
      <div class="card-col user">
        <div class="card user">
          <div class="row title">
            User Information
          </div>
          <div class="columns" v-if="selectedUser">
            <div class="column info">
              <div class="row">
                <div class="key"> Id</div>
                <div class="value">{{ selectedUser.id }}</div>
              </div>
              <div class="row">
                <div class="key"> Name</div>
                <div class="value">{{ selectedUser.name }}</div>
              </div>
              <div class="row">
                <div class="key"> Email</div>
                <div class="value">{{ selectedUser.email }}</div>
              </div>
              <div class="row">
                <div class="key"> Created At</div>
                <div class="value">{{ selectedUser.created_at }}</div>
              </div>
              <div class="row">
                <div class="key"> Expiration</div>
                <div class="value">
                  <div v-if="selectedUser.expiration_date != ''">{{ selectedUser.expiration_date }}</div>
                  <div class="user-disabled" v-else>User has been disabled</div>
                </div>
              </div>
              <div class="row">
                <div class="key"> Extra Animations</div>
                <div class="value">{{ selectedUser.settings.extra_animations ? 'yes' : 'no' }}</div>
              </div>
              <div class="row">
                <div class="key"> Extra Fonts</div>
                <div class="value">{{ selectedUser.settings.extra_fonts ? 'yes' : 'no' }}</div>
              </div>
              <div class="row">
                <div class="key"> Extra Icons</div>
                <div class="value">{{ selectedUser.settings.extra_icons ? 'yes' : 'no' }}</div>
              </div>
              <div class="row">
                <div class="key"> Color Buckets</div>
                <div class="value">{{ selectedUser.settings.color_buckets ? 'yes' : 'no' }}</div>
              </div>
              <div class="row">
                <div class="key"> WP Plugin</div>
                <div class="value">{{ selectedUser.settings.wordpress ? 'yes' : 'no' }}</div>
              </div>
              <div class="row">
                <div class="key"> WP Domains</div>
                <div class="value">{{ selectedUser.settings.num_domains }}</div>
              </div>
              <div class="row">
                <div class="key"> Save Templates</div>
                <div class="value">{{ selectedUser.settings.use_templates ? 'yes' : 'no' }}</div>
              </div>
              <div class="row">
                <div class="key"> Save Section Templates</div>
                <div class="value">{{ selectedUser.settings.section_templates ? 'yes' : 'no' }}</div>
              </div>
              <div class="row">
                <div class="key"> OTO2</div>
                <div class="value">
                  <span> {{ selectedUser.settings.use_templates ? 'yes' : 'no' }}</span>
                  <span v-if="selectedUser.monthly_expiration_date != ''">
                    (expiration: {{ selectedUser.monthly_expiration_date }})
                  </span>
                </div>
              </div>
              <div class="row">
                <div class="key"> Reloaded Upgrade</div>
                <div class="value"> {{ selectedUser.settings.oto3 ? 'yes' : 'no' }}</div>
              </div>
              <div class="row">
                <div class="key"> Is Youzign Customer</div>
                <div class="value">{{ selectedUser.youzign ? 'yes' : 'no' }}</div>
              </div>
              <div class="row">
                <div class="key"> Is Trial</div>
                <div class="value"> {{ selectedUser.settings.trial ? 'yes' : 'no' }}</div>
              </div>
              <div class="row">
                <div class="key"> Membership</div>
                <div class="value"> {{ selectedUser.settings.membership ? 'yes' : 'no' }}</div>
              </div>
              <div class="row">
                <div class="key"> Disable contests</div>
                <div class="value"> {{ selectedUser.settings.disable_contests ? 'yes' : 'no' }}</div>
              </div>
            </div>
            <div class="column controls">
              <div class="wp-domains">
                <el-input v-model="selectedUser.settings.num_domains"></el-input>
                <el-button type='primary' @click="setDomains">Set WP Domains</el-button>
              </div>
              <el-button type='primary' @click="removeTrial">Remove Trial</el-button>
              <el-button type='primary' @click="enableFeature('extraFeatures')">Enable Extra Features (OTO1)</el-button>
              <el-button type='primary' @click="enableFeature('monthlyTemplates')">Enable Monthly Templates (OTO2)
              </el-button>
              <el-button type='primary' @click="enableFeature('downsellTemplates')">Enable Templates (OTO2-Downsell)
              </el-button>
              <el-button type='primary' @click="enableFeature('reloadedUpgrade')">Enable Reloaded Upgrade</el-button>
              <el-button type='primary' @click="enableUser">Re-Enable User</el-button>
              <el-button type='primary' @click="disableUser">Disable User</el-button>
              <el-button type='primary' @click="disableFeature('extraFeatures')">Disable Extra Features (OTO1)
              </el-button>
              <el-button type='primary' @click="disableFeature('monthlyTemplates')">Disable Monthly Templates (OTO2)
              </el-button>
              <el-button type='primary' @click="disableFeature('downsellTemplates')">Disable Templates (OTO2-Downsell)
              </el-button>
              <el-button type='primary' @click="disableFeature('reloadedUpgrade')">Disable Reloaded Upgrade</el-button>
              <el-button type='primary' @click="toggleYouzign">
                <span v-if="selectedUser.youzign">Unmark&nbsp;</span>
                <span v-else>Mark&nbsp;</span>
                as YouZign customer
              </el-button>
              <el-button type='primary' @click="toggleMembership">
                <span v-if="selectedUser.settings.membership">Disable&nbsp;</span>
                <span v-else>Enable&nbsp;</span>
                Membership
              </el-button>
              <el-button type='primary' @click="toggleContests">
                <span v-if="!selectedUser.settings.disable_contests">Disable&nbsp;</span>
                <span v-else>Enable&nbsp;</span>
                Contests
              </el-button>
              <div class="password">
                <el-input placeholder="New Password" v-model="changePass"/>
                <el-button type="primary" :disabled="changePass.length < 10" @click="changePassword">change password
                </el-button>
                <div class="row error" v-if="changeSuccess">Password changed!</div>
              </div>
            </div>
          </div>
        </div>
        <div class="card user" v-if="selectedUser">
          <div class="row title">
            Transactions
          </div>
          <div class="row" v-if="!selectedUser.transactions || selectedUser.transactions.length == 0">
            No transaction found
          </div>
          <div class="transactions" v-else>
            <el-table stripe :data="selectedUser.transactions">
              <el-table-column label="Created at" prop="created_at"/>
              <el-table-column label="Platform" prop="platform"/>
              <el-table-column label="Transaction" prop="transaction"/>
              <el-table-column label="Id product" prop="id_product"/>
              <el-table-column label="Type" prop="type"/>
              <el-table-column label="Amount" prop="amount"/>
            </el-table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import AdminConfirmModal from "@/components/AdminConfirmModal";

export default {
  name: "Users",
  // components: {AdminConfirmModal},
  data: () => ({
    userSearch: '',
    userLoading: false,
    selectedUser: null,
    createType: '',
    createName: '',
    createEmail: '',
    createPass: '',
    changePass: '',
    changeSuccess: false,
    errMsg: null,
    searchFail: false,
    // confirmVisible: false,
    // confirmText: null,
    // confirmAction: null,
  }),
  methods: {
    search() {
      // this.userLoading = true
      this.$api.admin.searchUser({email: this.userSearch})
          .then((res) => {
            this.selectedUser = res.user
            this.selectedUser.transactions = res.transactions
            this.searchFail = false
          })
          .catch(() => {
            this.searchFail = true
          })
      // .finally(() => {
      //   this.userLoading = false
      // })
    },
    addUser() {
      this.$api.admin.addUser({
        name: this.createName,
        type: this.createType,
        email: this.createEmail,
        password: this.createPass,
      }).then(() => {
        this.errMsg = null
      }).catch((err) => {
        if (err.response && err.response.data.message) {
          this.errMsg = err.response.data.message
        }
      })
    },
    setDomains() {
      this.$api.admin.setDomains(this.selectedUser.id, this.selectedUser.settings.num_domains).finally(() => {
        this.search()
      })
    },
    removeTrial() {
      this.$api.admin.removeTrial(this.selectedUser.id).finally(() => {
        this.search()
      })
    },
    enableUser() {
      this.$api.admin.enableUser(this.selectedUser.id).finally(() => {
        this.search()
      })
    },
    disableUser() {
      this.$api.admin.disableUser(this.selectedUser.id).finally(() => {
        this.search()
      })
    },
    enableFeature(feature) {
      this.$api.admin.enable(this.selectedUser.id, feature).finally(() => {
        this.search()
      })
    },
    disableFeature(feature) {
      this.$api.admin.disable(this.selectedUser.id, feature).finally(() => {
        this.search()
      })
    },
    toggleYouzign() {
      this.$api.admin.toggleYouzign(this.selectedUser.id, !this.selectedUser.youzign).finally(() => {
        this.search()
      })
    },
    toggleMembership() {
      this.$api.admin.toggleMembership(this.selectedUser.id, !this.selectedUser.settings.membership).finally(() => {
        this.search()
      })
    },
    toggleContests() {
      this.$api.admin.toggleContests(this.selectedUser.id, !this.selectedUser.settings.disable_contests).finally(() => {
        this.search()
      })
    },
    changePassword() {
      this.$api.admin.changePassword(this.selectedUser.id, this.changePass).then(() => {
        this.changeSuccess = true
      })
    }
  }
}
</script>

<style lang="scss">
.users {

  .card {

    .el-button {
      width: 100%;
      font-size: 18px;
    }

    .el-select {
      width: 100%;
    }

    &.user {
      .el-button {
        width: 100%;
        font-size: 14px;
      }
    }
  }
}
</style>

<style lang="scss" scoped>
.users {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  padding: 0 20px;
  margin-bottom: 20px;

  .title {
    align-self: flex-start;
    font-size: 24px;
    font-weight: 600;
  }


  .cards {
    // TODO shrink on smaller devices
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    gap: 25px;
    justify-content: center;

    .card-col {
      display: flex;
      gap: 15px;
      flex-direction: column;
      width: 320px;


      &.user {
        width: 100%;
      }

      .card {
        width: 320px;
        //min-width: 320px;
        background-color: white;
        box-shadow: 0px 0px 28px rgba(0, 0, 0, 0.05);
        padding: 25px 25px;
        display: flex;
        flex-direction: column;
        gap: 10px;

        &.user {
          width: 100%
        }

        .columns {
          display: flex;
          gap: 10px;
          flex-direction: row;

          .column {
            //width: 100%;

            &.info {
              flex-grow: 2;
            }

            .row {
              display: flex;
              gap: 14px;

              .key {
                width: 170px;
                display: flex;
                justify-content: flex-end;
              }

              .value {
                font-weight: 700;
              }
            }

            &.info {
              display: flex;
              flex-direction: column;
            }

            &.controls {
              display: flex;
              flex-direction: column;
              gap: 8px;

              .wp-domains {
                display: flex;
                gap: 10px;

                .el-input {
                  width: 80px;
                }

                .el-button {

                }
              }

              .el-button {
                margin: 0;
              }
            }
          }
        }

        .row {
          height: 40px;

          .user-disabled {
            color: $clr-red;
            font-weight: 700;
          }

          &.title {
            height: 60px;
            font-size: 22px;
          }

          &.error {
            font-size: 18px;
            color: #f0c600;
            display: flex;
            justify-content: center;
            align-items: center;
          }
        }
      }
    }
  }
}
</style>